import React, { Component } from 'react';
import _ from 'lodash';
import {
  Step,
  Icon,
  Segment,
  Header,
  Message,
  Input,
  Dropdown,
  Button
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'
import { deployerActionDirect } from '../Utils/APIUtil.js';
import { capitalize } from '../Utils/CommonUtil';
import CONSTANTS from '../Utils/Constants';
import { parseBuildInfo } from './CustomComponents';

const { ACTIONS } = CONSTANTS;

class DeployWizard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      step: 0,
      selectedEntity: '',
      version: null,
      deployResponse: {
        text: 'We are fetching that content for you.',
        title: 'Just one second',
        isPositive: false
      },
      loading: true,
      backupData: {},
      tunnelsData: []
    }
  }

  handleChangeEntity = (value, stateName) => {
    this.setState({
      [stateName]: value,
      version: null
    });
  }

  handleChangeVersion = (e, { value }) => this.setState({ version: value })

  handleNext = () => {
    let { step } = this.state;
    if (step < 2) {
      step = step + 1;
      this.setState({ step });
    }
  }

  handleBack = () => {
    let { step } = this.state;
    if (step > 0) {
      step = step - 1;
      this.setState({ step });
    }
  }

  handleDeploy = async () => {
    const { selectedEntity, version } = this.state;
    this.setState({
      deployResponse: {
        text: 'Please wait while the build is being deployed.',
        title: 'Deploying',
        isPositive: true
      },
      loading: true
    });

    let response = {};
    response = await deployerActionDirect(ACTIONS.DEPLOY_ENTITY, { entity:selectedEntity, filename: version });
    response = _.get(response, 'data', {});
    let { logs, message, status } = response;
    logs = logs || [];

    const deployResponse = {
      text: logs.join('. '),
      title: message,
      isPositive: (status === 'success')
    };

    this.setState({
      loading: false,
      deployResponse
    });
  }

  async componentDidMount() {
    let backupData = await deployerActionDirect(ACTIONS.BACKUP_STATUS);
    let tunnelsData = await deployerActionDirect(ACTIONS.GET_TUNNELS);

    backupData = _.get(backupData, 'data', {});
    tunnelsData = _.get(tunnelsData, 'data.rows', []);

    this.setState({
      loading: false,
      backupData,
      tunnelsData,
      deployResponse: {
        text: 'We are ready to deploy the builds.',
        title: 'Ready',
        isPositive: true
      }
    });
  }

  _isButtonDisabled = (loading, isVersionSelected, isbuildPesent, step) => {
    switch (step) {
      case 0:
        return (loading);
      case 1:
        return (isVersionSelected && !isbuildPesent);
      case 2:
        return (isVersionSelected && !isbuildPesent);
      default:
        return false;
    }
  }

  _versionSearchSelection = (data) => {
    const options = data.map((item) => {
      return { key: item.name, value: item.name, flag: item.name, text: item.name }
    });

    return (
      <div className="search-version-dropdown">
        <Dropdown
          value={this.state.version}
          placeholder='Select Version'
          onChange={this.handleChangeVersion}
          fluid
          search
          selection
          options={options} />
      </div>
    );
  }

  _stepsWizard = (step, isPositiveResponse) => {
    return (
      <Step.Group ordered>
        <Step completed={(step > 0)}>
          <Step.Content>
            <Step.Title>Entity Type</Step.Title>
            <Step.Description>Choose your entity type</Step.Description>
          </Step.Content>
        </Step>

        <Step completed={(step > 1)}>
          <Step.Content>
            <Step.Title>Version</Step.Title>
            <Step.Description>Choose version of your entity</Step.Description>
          </Step.Content>
        </Step>

        <Step completed={(step > 1) && isPositiveResponse}>
          <Step.Content>
            <Step.Title>Deploy</Step.Title>
            <Step.Description>Deploy the entity</Step.Description>
          </Step.Content>
        </Step>
      </Step.Group>
    );
  }

  _renderPage = () => {
    const { backupData, selectedEntity, loading,
      deployResponse, step, tunnelsData, version } = this.state;
    let disableButton = true;
    let _renderValidationMessage = <div />;
    const isVersionSelected = (version !== null);

    const entityOptions = tunnelsData.map((item, index) => {
      return { key: (index + 1), text: `Deploy Latest ${capitalize(item.entity)}`, value: item.entity };
    });

    const entityDetails = _.find(tunnelsData, tunnel => {
      return (tunnel.entity === selectedEntity);
    }) || {};
    const remotePath = _.get(entityDetails, 'remote_path', '-');
    const entityName = _.get(entityDetails, 'entity', '-');

    let entiyBuildData = _.get(backupData, `data.${remotePath}`, []);
    entiyBuildData = _.filter(entiyBuildData, buildItem => {
      return buildItem.name.includes('.FN.');
    }) || [];
    const entiyBuildDataSelected = _.find(entiyBuildData, (item) => {
      return (item.name === version);
    }) || {};
    const isBuildPresent = (_.get(entiyBuildDataSelected, 'path', null) !== null);

    _renderValidationMessage = (parseBuildInfo(`${entityName} Build Information`, entiyBuildDataSelected,
      isBuildPresent, isVersionSelected, backupData.source));
    disableButton = this._isButtonDisabled(loading, isVersionSelected, isBuildPresent, step);

    const _sectionOne = () => {
      return (
        <Segment>
          <Dropdown
            disabled={loading}
            onChange={(e, { value }) => this.handleChangeEntity(value, 'selectedEntity')}
            options={entityOptions}
            placeholder='Choose an entity'
            selection
            value={selectedEntity}
          />
          <div className="deploy-wizard-message-container">
            <Message>
              <Message.Header>Deployment Entity</Message.Header>
              <p>{`This Deploys a version of the ${entityName} on your hosting.`}</p>
            </Message>
          </div>
        </Segment>
      );
    };

    const _sectionTwo = () => {
      return (
        <>
          <Segment>
            {this._versionSearchSelection(entiyBuildData)}
            {_renderValidationMessage}
          </Segment>
        </>);
    };

    const _sectionThree = () => {
      return (
        <>
          <Segment>
            <div>
              <Input label='Entity' value={selectedEntity} disabled />
              <Input className="build-file-name" label='Build File' value={entiyBuildDataSelected.name} disabled />
            </div>
            {_renderValidationMessage}
          </Segment>
        </>);
    };

    return (
      <Segment.Group>
        <Header as='h5' attached='top'>
          <div>Deploy Wizard</div>
        </Header>
        <Segment>
          <Message>
            <Message.Header>What does it do?</Message.Header>
            <p>This workflow deploys the selected versioned builds.</p>
          </Message>
        </Segment>
        <Segment>
          {this._stepsWizard(step, deployResponse.isPositive)}
        </Segment>
        {(step === 0) && _sectionOne()}
        {(step === 1) && _sectionTwo()}
        {(step === 2) && _sectionThree()}
        <Segment>
          <Button
            onClick={() => this.handleBack()}
            disabled={loading || (step < 1)}
            loading={loading}>
            Back
          </Button>
          <Button
            onClick={() => (step > 1) ? this.handleDeploy() : this.handleNext()}
            disabled={disableButton}
            loading={loading}
            primary>{(step > 1) ? `Deploy` : `Next`}</Button>
          <Message icon positive={deployResponse.isPositive && !loading} negative={!deployResponse.isPositive && !loading}>
            <Icon name='circle notched' loading={loading} />
            <Message.Content>
              <Message.Header>{deployResponse.title}</Message.Header>
              {deployResponse.text}
            </Message.Content>
          </Message>
        </Segment>
      </Segment.Group>
    );
  }

  render() {
    return (this._renderPage());
  }
}

export default DeployWizard;

