import React from 'react'
import { Button, Modal, Message } from 'semantic-ui-react';
import { TransitionModal } from 'semantic-ui-react-transition-modal';

export function DeleteJob({ trigger, execute, selectedEntities }) {
    const [open, setOpen] = React.useState(false)

    return (
        <TransitionModal
            closeOnDimmerClick={false}
            animation="fade up"
            duration={200}
            onClose={() => setOpen(false)}
            onOpen={() => setOpen(true)}
            open={open}
            trigger={trigger}
        >
            <Modal.Header>Delete Job</Modal.Header>
            <Modal.Content image>
                <Modal.Description>
                    <Message warning>
                        <Message.Header>Backup shutdown imminent</Message.Header>
                        <p>Deleting this job will shutdown the auto backup process for this particular entity.</p>
                    </Message>
                    <p>Are you sure you want to delete <b>{selectedEntities.join(", ")}</b> ?</p>
                </Modal.Description>
            </Modal.Content>
            <Modal.Actions>
                <Button onClick={() => setOpen(false)}>Cancel</Button>
                <Button
                    onClick={() => {
                        setOpen(false);
                        execute();
                    }}
                    negative
                >
                    Delete
                </Button>
            </Modal.Actions>
        </TransitionModal>
    )
}

