import React, { Component } from 'react';
import {
    Grid,
    Popup,
    Message,
    Card,
    Button,
    Checkbox,
    Form
} from 'semantic-ui-react'
import _ from 'lodash';
import AppTitle from './AppTitle';
import Logo from '../Images/logo.svg';
import { validateInstanceURL, login, deployerActionDirect } from '../Utils/APIUtil';
import { getInstanceURL, setInstanceURL, unsetInstanceURL, setLogin, refreshPage } from '../Utils/CommonUtil';
import { StatusIcon, serviceDownSegment, loaderCustom } from './CustomComponents';
import CONSTANTS from '../Utils/Constants';

const { ACTIONS } = CONSTANTS;

class Login extends Component {
    constructor(props) {
        super(props);

        this.state = {
            instanceLoading: false,
            dbHealth: {},
            storageHealth: {},
            isDatabaseUP: false,
            isStorageUP: false,
            loading: true,
            instanceId: null,
            username: '',
            password: '',
            errors: []
        }
    }

    componentDidMount() {
        this.getInitialData();
    }

    getInitialData = async () => {
        let dbHealth = await deployerActionDirect(ACTIONS.DB_STATUS);
        let storageHealth = await deployerActionDirect(ACTIONS.STORAGE_HEALTH);
        dbHealth = _.get(dbHealth, 'data', {});
        storageHealth = _.get(storageHealth, 'data', {});
        const isDatabaseUP = (_.get(dbHealth, 'status', 'failed') === 'success');
        const isStorageUP = (_.get(storageHealth, 'status', 'failed') === 'success');
        const instanceURL = getInstanceURL();
        const isInstanceSet = (instanceURL !== null);

        this.setState({
            instanceLoading: false,
            instanceURL,
            isInstanceSet,
            dbHealth,
            storageHealth,
            isDatabaseUP,
            isStorageUP,
            loading: false
        });
    }

    onChange = (e, stateName) => {
        this.setState({
            [stateName]: e.target.value,
            errors: []
        });
    }

    onClickInstanceValidation = async () => {
        this.setState({
            instanceLoading: true
        });
        const { isValid, error, url } = await validateInstanceURL(this.state.instanceId);

        if (isValid) {
            setInstanceURL(url);
            this.getInitialData();
        } else {
            this.setState({
                instanceLoading: false,
                instanceURL: null,
                isInstanceSet: false,
                loading: false,
                errors: error
            });
        }
    }

    onClickLogin = async () => {
        const { username, password } = this.state;
        this.setState({
            loading: true
        });
        const { loginStatus, error } = await login(username, password);

        if (loginStatus) {
            setLogin(username, password);
            this.setState({
                loading: false,
            });
            refreshPage();
        } else {
            this.setState({
                loading: false,
                errors: error
            });
        }
    }

    _renderForm = () => {
        const { loading, errors, isInstanceSet, isDatabaseUP,
            isStorageUP, instanceLoading } = this.state;
        const hasErrors = !_.isEmpty(errors);

        const alert = () => {
            return (
                <Message className={`alert-message-login ${hasErrors ? '' : 'invisible'}`} negative>
                    <p>{errors.join(',')}</p>
                </Message>
            );
        };

        const instanceForm = () => {
            return (
                <Form>
                    <Form.Input
                        onChange={(e) => this.onChange(e, 'instanceId')}
                        label="Instance URL"
                        loading={loading}
                        name="instance"
                        type="text" />
                    <Button loading={instanceLoading}
                        onClick={() => this.onClickInstanceValidation()}
                        type='submit'>Set</Button>
                </Form>
            );
        }

        const loginForm = () => {
            return (
                <Form autoComplete="off">
                    <Form.Input
                        onChange={(e) => this.onChange(e, 'username')}
                        label="Username" name="username" type="text" />
                    <Form.Input
                        onChange={(e) => this.onChange(e, 'password')}
                        label="Password" name="password" type="password" />
                    <Form.Field>
                        <Checkbox label='Remember Me' />
                    </Form.Field>
                    <Button loading={loading} onClick={() => this.onClickLogin()} type='submit'>Login</Button>
                </Form>
            );
        }

        const cardInfo = () => {
            return (
                <div className="login-service-info flex-row flex-jc-sb">
                    <Popup
                        trigger={
                            <div className="flex-row">
                                <div>{StatusIcon(isInstanceSet ? true : null)}</div>
                                <a href>Deployer Server</a>
                            </div>

                        }
                        content={`Server endpoint ${isDatabaseUP ? getInstanceURL() : 'is not set'}`}
                        inverted
                    />
                    <Popup
                        trigger={
                            <div className="flex-row">
                                <div>{StatusIcon(isDatabaseUP ? true : null)}</div>
                                <a href>Database</a>
                            </div>

                        }
                        content={`Database service ${isDatabaseUP ? 'is up' : 'disconnected'}`}
                        inverted
                    />
                    <Popup
                        trigger={
                            <div className="flex-row">
                                <div>{StatusIcon(isStorageUP ? true : null)}</div>
                                <a href>Storage</a>
                            </div>

                        }
                        content={`Storage service ${isStorageUP ? 'is up' : 'disconnected'}`}
                        inverted
                    />
                </div>
            );
        }

        return (
            <Card className="login-card w-100">
                <Card.Content>
                    <div className="content-middle-container">
                        <div className="logo-container">
                            <img style={{ height: 75, width: 75 }} alt="Lazarus Deployer" src={Logo} />
                        </div>
                        <Card.Header>
                            <AppTitle />
                        </Card.Header>
                        {alert()}
                        <Card.Description>
                            {isInstanceSet ? loginForm() : instanceForm()}
                        </Card.Description>
                    </div>
                </Card.Content>
                <Card.Content extra>
                    <div className="content-middle-container content-extra">
                        {cardInfo()}
                    </div>
                </Card.Content>
            </Card>
        );
    }

    _renderComponents = () => {
        const { dbHealth, storageHealth, isStorageUP,
            isDatabaseUP, isInstanceSet, loading } = this.state;
        const compositeObjectSD = {
            dbHealth, storageHealth,
            onClickRetry: refreshPage,
            onClickDisconnect: () => {
                unsetInstanceURL();
                refreshPage();
            }
        };
        let isServiceDown = false;

        if (isInstanceSet) {
            if (!isDatabaseUP || !isStorageUP) {
                isServiceDown = true;
            }
        }

        if (loading && !isInstanceSet) {
            return (loaderCustom());
        }

        return (
            <Grid stretched columns={3} verticalAlign="middle" className="grid-full-height">
                <Grid.Row>
                    <Grid.Column></Grid.Column>
                    <Grid.Column>
                        {isServiceDown ? serviceDownSegment(compositeObjectSD) : this._renderForm()}
                    </Grid.Column>
                    <Grid.Column></Grid.Column>
                </Grid.Row>
            </Grid>
        );
    }

    render() {
        return (<div className="login-main-container">
            {this._renderComponents()}
        </div>);
    }

}

export default Login;