import React from 'react'
import { Button, Modal } from 'semantic-ui-react';
import { TransitionModal } from 'semantic-ui-react-transition-modal';

export default function RevokeTunnel({ trigger, execute, selectedEntities }) {
    const [open, setOpen] = React.useState(false)

    return (
        <TransitionModal
            closeOnDimmerClick={false}
            animation="fade up"
            duration={200}
            onClose={() => setOpen(false)}
            onOpen={() => setOpen(true)}
            open={open}
            trigger={trigger}
        >
            <Modal.Header>Revoke Tunnel</Modal.Header>
            <Modal.Content image>
                <Modal.Description>
                    <p>Are you sure you want revoke <b>{selectedEntities.join(", ")}</b> ?</p>
                </Modal.Description>
            </Modal.Content>
            <Modal.Actions>
                <Button onClick={() => setOpen(false)}>Cancel</Button>
                <Button
                    onClick={() => {
                        setOpen(false);
                        execute();
                    }}
                    negative
                >
                    Revoke
                </Button>
            </Modal.Actions>
        </TransitionModal>
    )
}
