import React, { Component } from 'react';
import NavigationBar from './NavigationBar';
import ReactNotification from 'react-notifications-component';
import 'react-notifications-component/dist/theme.css'
import 'animate.css/animate.compat.css'
import Menu from './Menu';
import 'semantic-ui-css/semantic.min.css'
import { isloggedIn } from '../Utils/APIUtil.js';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loggedIn: false
    }
  }

  async componentDidMount() {
    const loggedIn = await isloggedIn();
    this.setState({
      loggedIn
    });
    this.props.setAppLoader(false);
  }

  _renderPage = () => {
    return (
      <div>
        <ReactNotification />
        <NavigationBar ref={instance => { this.content = instance; }} setAppLoader={this.props.setAppLoader} />
        <Menu refetchNotification={() => this.content._getIntitalData()} />
      </div>
    );
  }

  render() {
    return (this._renderPage());
  }
}

export default App;

