import React, { Component } from 'react';
import _ from 'lodash';
import {
  Input,
  Dropdown,
  Icon,
  Button,
  Table,
  Segment,
  Header,
  Message,
  Pagination,
  Checkbox,
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'
import { deployerActionDirect } from '../Utils/APIUtil.js';
import {
  capitalize, successNotification, errorNotification,
  showBusyMessage, updateLoadingProgress, progressContinue
} from '../Utils/CommonUtil';
import { LoaderProgress } from './CustomComponents';
import CONSTANTS from '../Utils/Constants';
import DeleteTunnel from './DeleteTunnel.jsx';

const { ACTIONS } = CONSTANTS;
const ENTITY_TABLE = 'Backup';

export default class BackupsExplorer extends Component {
  constructor(props) {
    super(props);

    this.tableRowLength = 5;

    this.state = {
      activePage: 1,
      value: 1,
      loading: true,
      selectedDirs: [],
      backupDirs: [],
      backupData: [],
      backupDataSource: [],
      selectedEntities: [],
      loadingPercentage: 10
    }

    this.updateLoadingProgress = updateLoadingProgress.bind(this);
    this.progressContinue = progressContinue.bind(this);
  }

  async componentDidMount() {
    await this._getIntitalData();
  }

  componentWillUnmount() {
    clearInterval(this.timer);
  }

  setLoading = (value) => {
    this.setState({
      loading: value
    });
  }

  resetSelection = () => {
    this.setState({
      selectedEntities: []
    });
  }

  _getIntitalData = async () => {
    this.timer = setInterval(() => this.progressContinue(), 25);
    let backupData = await deployerActionDirect(ACTIONS.BACKUP_STATUS);
    backupData = _.get(backupData, 'data.data', []);
    let allBackups = [];
    let backupDirs = [];
    for (const [key, value] of Object.entries(backupData)) {
      allBackups = allBackups.concat(value);
      backupDirs = backupDirs.concat(key);
    }
    backupData = allBackups;
    const backupResultData = backupData;
    const backupDataSource = backupData;

    this.updateLoadingProgress(90);
    clearInterval(this.timer);

    this.setState({
      loading: false,
      backupData,
      backupDirs,
      search: '',
      backupDataSource,
      backupResultData,
      selectedEntities: []
    });

    this.handlePaginationChange({ activePage: 1 });
  }

  _paginationHelper = (resultData, activePage) => {
    let backupData = [];
    resultData = _.sortBy(resultData, (item) => item.server_modified);
    resultData = _.reverse(resultData);
    for (let index = ((activePage - 1) * this.tableRowLength); index <= ((activePage * this.tableRowLength) - 1); index++) {
      if (index < resultData.length) {
        backupData.unshift(resultData[index]);
      }
    }
    return backupData;
  }

  handlePaginationChange = ({ activePage }) => {
    let { backupResultData } = { ...this.state };
    const backupData = this._paginationHelper(backupResultData, activePage);
    this.setState({
      backupData,
      activePage
    });
  }

  onChangeSearch = (e) => {
    const search = e.target.value;
    let { backupDataSource } = this.state;
    let backupResultData = _.filter(backupDataSource, tunnel => {
      return tunnel.name.includes(search);
    });
    const backupData = this._paginationHelper(backupResultData, 1);
    this.setState({
      backupData,
      backupResultData,
      search,
      errors: []
    });

  }

  onSelectChange = (value, stateName) => {
    const { backupDataSource } = { ...this.state };
    let filteredBackups = backupDataSource.filter(item => {
      if (value.length === 0) {
        return true;
      }
      return (value.includes(item.dir));
    });
    const backupData = this._paginationHelper(filteredBackups, 1);
    this.setState({ [stateName]: value, backupResultData: filteredBackups, backupData, activePage: 1 });
  }

  handleDeleteAction = async () => {
    const { selectedEntities } = this.state;
    const entity = _.first(selectedEntities);
    this.setLoading(true);
    let response = await deployerActionDirect(ACTIONS.DELETE_TUNNEL, { entity });
    response = _.get(response, 'data', {});
    const message = _.get(response, 'message', 'Deletion failed')
    if (response.status === "success") {
      successNotification(message);
    } else {
      errorNotification(message);
    }
    await this._getIntitalData();
  }

  handleDownloadAction = async () => {
    const { selectedEntities, backupDataSource } = this.state;
    const entity = _.first(selectedEntities);
    const fileObject = _.find(backupDataSource, item => {
      return (item.name === entity);
    }) || {};
    const filepath = _.get(fileObject, 'path', '');
    let response = await deployerActionDirect(ACTIONS.GET_BACKUP_LINK, { filepath });
    response = _.get(response, 'data', {});
    const message = _.get(response, 'message', 'Download failed');
    const link = _.get(response, 'link', '');
    const filename = _.get(response, 'details.name', '');
    const size = _.get(response, 'details.size', '');
    const notificationEl = (<div>
      <div>{filename}</div>
      <div>{size}</div>
      <div>{message}</div>
      <Button positive className="mt-20" icon href={link} labelPosition='right'>
        Download
        <Icon name='download' />
      </Button>
    </div>);
    if (response.status === "success") {
      successNotification(notificationEl, "Success", 120);
    } else {
      errorNotification(message);
    }
    this.resetSelection();
  }

  handleSelect = (username) => {
    let { selectedEntities } = { ...this.state };
    const entitySelected = _.includes(selectedEntities, username);
    if (entitySelected) {
      selectedEntities = _.remove(selectedEntities, function (entity) {
        return (entity !== username);
      });
    } else {
      selectedEntities.push(username);
    }
    this.setState({
      selectedEntities
    });
  }

  _renderTable = (data) => {
    let tableRowData = [
      {
        width: 1,
        header: '',
        dataPath: ['name'],
        formatter: (entity) => (
          <Checkbox name={entity} onChange={() => this.handleSelect(entity)}
            checked={_.includes(this.state.selectedEntities, entity)} />)
      },
      {
        width: 4,
        header: 'File',
        dataPath: ['name'],
        formatter: capitalize
      },
      {
        width: 2,
        header: 'Size',
        dataPath: ['size'],
      },
      {
        width: 4,
        header: 'ETA',
        dataPath: ['eta'],
      }
    ];

    const tableData = data.map(row => {
      return tableRowData.map(item => {
        const hasFormater = _.get(item, 'formatter', null);
        if (hasFormater) {
          // Handle if function output is passed instead of reference
          try {
            const results = item.dataPath.map(dp => {
              if (dp === '[object]') {
                return row;
              }
              return _.get(row, dp, '-');
            });
            return hasFormater(...results);
          } catch {
            return hasFormater || '-';
          }
        }
        return _.get(row, item.dataPath[0], '-');
      });
    });

    if (_.isEmpty(data)) {
      return (LoaderProgress(this.state.loadingPercentage));
    }

    let totalPages = Math.ceil(this.state.backupResultData.length / this.tableRowLength);

    return (
      <div>
        <div className="w-100 flex-row flex-jc-fe pt-10">
          <Pagination
            firstItem={null}
            lastItem={null}
            activePage={this.state.activePage}
            onPageChange={(e, { activePage }) => this.handlePaginationChange({ activePage })}
            totalPages={totalPages}
          />
        </div>
        <Table celled>
          <Table.Header>
            <Table.Row>
              {
                tableRowData.map(item => {
                  return <Table.HeaderCell width={item.width}>{item.header}</Table.HeaderCell>;
                })
              }
            </Table.Row>
          </Table.Header>

          <Table.Body>
            {
              tableData.map(row => {
                return <Table.Row>{
                  row.map(cell => {
                    return <Table.Cell>{cell}</Table.Cell>
                  })
                }</Table.Row>;
              })
            }
          </Table.Body>
        </Table>
      </div>
    )
  }

  _renderTableMessage = (data) => {
    const { loading } = this.state;
    let content = <p>Viewing all {data.length} {ENTITY_TABLE}</p>;
    if (loading) {
      content = <p>{<Icon name='circle notched blue' loading />}{showBusyMessage()}</p>;
    }
    return (<Message>{content}</Message>);
  }

  _renderPage = () => {
    const { backupData, backupDataSource,
      selectedEntities, backupDirs, loading } = this.state;

    const dirOptions = backupDirs.map(item => {
      return {
        key: item,
        text: item,
        value: item,
      };
    });

    return (
      <Segment.Group>
        <Header as='h5' attached='top'>
          <div>{ENTITY_TABLE} Explorer</div>
        </Header>
        <Segment>
          <div className="flex-row flex-jc-sb w-100">
            <div>
              <Dropdown
                disabled={(selectedEntities.length !== 1)}
                button
                text='Actions'>
                <Dropdown.Menu closeOnBlur={true} closeOnChange={true}>
                  <DeleteTunnel
                    execute={this.handleDeleteAction}
                    selectedEntities={selectedEntities}
                    trigger={< Dropdown.Item disabled={(selectedEntities.length !== 1)} text='Delete' />} />
                  <Dropdown.Item onClick={this.handleDownloadAction} disabled={(selectedEntities.length !== 1)} text='Download' />
                </Dropdown.Menu>
              </Dropdown>
            </div>
            <Input disabled={loading} icon='search' onChange={(e) => this.onChangeSearch(e)} placeholder='Search...' />
          </div>
          <div>
            <Dropdown
              disabled={loading}
              loading={loading}
              className="mt-20"
              placeholder='All Directories'
              onChange={(e, { value }) => this.onSelectChange(value, 'selectedDirs')}
              multiple
              search
              fluid
              selection
              options={dirOptions}
            />
          </div>
          {this._renderTableMessage(backupDataSource)}
        </Segment>
        <Segment className="table-segment">
          {this._renderTable(backupData)}
        </Segment>
      </Segment.Group>
    );
  }

  render() {
    return (this._renderPage());
  }
}
