import axios from 'axios';
import _ from 'lodash';
import { getInstanceURL, getLogin } from './CommonUtil.js';
const qs = require('qs');


export const deployerAction = async (body,
    BASE_URL = getInstanceURL()) => {
    try {
        const res = await axios.post(`${BASE_URL}`, qs.stringify(body));
        const data = _.get(res, 'data', []);

        return {
            data,
        };
    } catch (e) {
        console.error(e);
        return {
            data: [],
            error: e
        };
    }
};

export const deployerActionDirect = async (action, option = null) => {
    const { username, password } = getLogin();
    let body = { action, username, password };
    if (option !== null)  {
        body = _.extend(body, option);
    }
    const response = await deployerAction(body);
    return (response);
};

export const validateInstanceURL = async (url) => {
    const body = { action: 'health' };
    const response = await deployerAction(body, url);
    const isAlive = _.get(response, 'data.isAlive', false);
    const error = _.get(response, 'error', null);

    if (!isAlive) {
        const thisError = error ? error : 'This API is not deployer instance';
        return ({
            url,
            error: [thisError],
            isValid: false
        });
    }

    return ({
        url,
        error: [],
        isValid: true
    });
};

export const login = async (username, password) => {
    const body = { action: 'login', username, password };
    const response = await deployerAction(body);

    let loginStatus = _.get(response, 'data.status', 'failed');
    loginStatus = (loginStatus === 'failed') ? false : true;
    const error = _.get(response, 'data.message', 'Login Failed');

    if (!loginStatus) {
        const thisError = error ? error : 'Login Failed';
        return ({
            error: [thisError],
            loginStatus: false
        });
    }

    return ({
        error: [],
        loginStatus: true
    });
};

export const isloggedIn = async () => {
    const { username, password } = getLogin();
    const body = { action: 'login', username, password };
    const response = await deployerAction(body);

    let loginStatus = _.get(response, 'data.status', 'failed');
    loginStatus = (loginStatus === 'failed') ? false : true;
    return loginStatus;
};