import React, { Component } from 'react';
import _ from 'lodash';
import {
  Input,
  Table,
  Segment,
  Header,
  Message,
  Pagination,
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'
import { deployerActionDirect } from '../Utils/APIUtil.js';
import { capitalize, successNotification } from '../Utils/CommonUtil';
import { LoaderProgress, extraItemsPopup } from './CustomComponents';
import CONSTANTS from '../Utils/Constants';

const { ACTIONS } = CONSTANTS;
const ENTITY_TABLE = 'Activities';

export default class Activities extends Component {
  constructor(props) {
    super(props);

    this.tableRowLength = 10;

    this.state = {
      activePage: 1,
      showConfirm: false,
      versioned: true,
      value: 1,
      loading: true,
      logsData: [],
      logsDataSource: [],
      selectedEntities: [],
      loadingPercentage: 10
    }
  }

  async componentDidMount() {
    await this._getIntitalData();
  }

  markLoadingProgress = (loadingPercentage) => {
    this.setState({
      loadingPercentage
    });
  }

  _getIntitalData = async () => {
    let logsData = await deployerActionDirect(ACTIONS.GET_LOGS);
    logsData = _.get(logsData, 'data.rows', []);
    this.markLoadingProgress(90);
    const logsDataSource = logsData;

    this.setState({
      loading: false,
      logsData,
      logsDataSource,
      selectedEntities: [],
    });

    this.handlePaginationChange(null, { activePage: 1 });
  }

  handlePaginationChange = (e, { activePage }) => {
    let { logsDataSource } = { ...this.state };
    let logsData = [];
    for (let index = ((activePage - 1) * this.tableRowLength); index <= ((activePage * this.tableRowLength) - 1); index++) {
      if (index < logsDataSource.length) {
        logsData.unshift(logsDataSource[index]);
      }
    }

    this.setState({
      logsData,
      activePage
    });
  }

  handleConfirm = (value) => {
    this.setState({
      showConfirm: value
    });
  }

  handleConfirmAction = async () => {
    const { selectedEntities } = this.state;
    const entity = _.first(selectedEntities);
    let response = await deployerActionDirect(ACTIONS.DELETE_USER, { target_username: entity });
    successNotification(_.get(response, 'message', 'Deletion failed'));
    this.handleConfirm(false);
    await this._getIntitalData();
  }

  handleSelect = (username) => {
    let { selectedEntities } = { ...this.state };
    const entitySelected = _.includes(selectedEntities, username);
    if (entitySelected) {
      selectedEntities = _.remove(selectedEntities, function (entity) {
        return (entity !== username);
      });
    } else {
      selectedEntities.push(username);
    }
    this.setState({
      selectedEntities
    });
  }

  _formatTimeStamp = (timestamp, eta) => {
    return <div title={timestamp}>{eta}</div>;
  }
  _formatDetails = (details) => {
    return extraItemsPopup(details.split(';'));
  }

  _renderTable = (data) => {
    let tableRowData = [
      {
        width: 1,
        header: 'ETA',
        dataPath: ['timestamp', 'eta'],
        formatter: this._formatTimeStamp

      },
      {
        width: 2,
        header: 'Action',
        dataPath: ['step'],
        formatter: capitalize
      },
      {
        width: 2,
        header: 'Entity',
        dataPath: ['source'],
        formatter: capitalize

      },
      {
        width: 4,
        header: 'Details',
        dataPath: ['details'],
        formatter: this._formatDetails
      }
    ];

    const tableData = data.map(row => {
      return tableRowData.map(item => {
        const hasFormater = _.get(item, 'formatter', null);
        if (hasFormater) {
          // Handle if function output is passed instead of reference
          try {
            const results = item.dataPath.map(dp => {
              if (dp === '[object]') {
                return row;
              }
              return _.get(row, dp, '-');
            });
            return hasFormater(...results);
          } catch {
            return hasFormater || '-';
          }
        }
        return _.get(row, item.dataPath[0], '-');
      });
    });

    if (_.isEmpty(data)) {
      return (LoaderProgress(this.state.loadingPercentage));
    }

    return (
      <div>
        <div className="w-100 flex-row flex-jc-fe">
          <Pagination
            firstItem={null}
            lastItem={null}
            activePage={this.state.activePage}
            onPageChange={this.handlePaginationChange}
            totalPages={Math.ceil(this.state.logsDataSource.length / this.tableRowLength)}
          />
        </div>
        <Table celled>
          <Table.Header>
            <Table.Row>
              {
                tableRowData.map(item => {
                  return <Table.HeaderCell width={item.width}>{item.header}</Table.HeaderCell>;
                })
              }
            </Table.Row>
          </Table.Header>

          <Table.Body>
            {
              tableData.map(row => {
                return <Table.Row>{
                  row.map(cell => {
                    return <Table.Cell>{cell}</Table.Cell>
                  })
                }</Table.Row>;
              })
            }
          </Table.Body>
        </Table>
      </div>
    )
  }

  _renderPage = () => {
    const { logsData, logsDataSource } = this.state;

    return (
      <Segment.Group>
        <Header as='h5' attached='top'>
          <div>{ENTITY_TABLE}</div>
        </Header>
        <Segment>
          <Input icon='search' placeholder='Search...' />
          <Message>
            <p>Viewing all {logsDataSource.length} {ENTITY_TABLE}</p>
          </Message>
        </Segment>
        <Segment>
          {this._renderTable(logsData)}
        </Segment>
      </Segment.Group>
    );
  }

  render() {
    return (this._renderPage());
  }
}
