import React, { Component } from 'react';
import _ from 'lodash';
import {
  Dropdown,
  Button,
  Icon,
  Segment,
  Card,
  Divider,
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'
import { deployerActionDirect } from '../Utils/APIUtil.js';
import { capitalize, successNotification, errorNotification } from '../Utils/CommonUtil';
import CONSTANTS from '../Utils/Constants';
import { LoaderProgress, StatusHoop, listRow } from './CustomComponents';
import ModalAddJob from './ModalAddJob.jsx';
import { DeleteJob, UpdateStateJob } from './ActionsModalJob.jsx';

const { ACTIONS } = CONSTANTS;
const ENTITY_TABLE = 'Job';

export default class Scheduler extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loadingPercentage: 15,
      loading: true,
      schedulerData: {},
      storageData: {},
      dbData: {}
    }
  }

  setLoading = (value) => {
    this.setState({
      loading: value
    });
  }

  markLoadingProgress = (loadingPercentage) => {
    this.setState({
      loadingPercentage
    });
  }

  componentDidMount() {
    this._getIntitalData();
  }

  _getIntitalData = async () => {
    let schedulerData = await deployerActionDirect(ACTIONS.GET_SCHEDULER);
    this.markLoadingProgress(25);

    schedulerData = _.get(schedulerData, 'data.rows', {});

    this.setState({
      loading: false,
      schedulerData
    });
  }

  handleDeleteAction = async (selectedEntities) => {
    const id = _.first(selectedEntities);
    let response = await deployerActionDirect(ACTIONS.DELETE_SCHEDULER, { id });
    response = _.get(response, 'data', {});
    const message = _.get(response, 'message', 'Deletion failed')
    if (response.status === "success") {
      successNotification(message);
    } else {
      errorNotification(message);
    }
    await this._getIntitalData();
  }

  handleChangeStateAction = async (selectedEntities, stateValue) => {
    const id = _.first(selectedEntities);
    const state = stateValue ? 'running' : 'stopped';
    let response = await deployerActionDirect(ACTIONS.UPDATE_SCHEDULER, { id, state });
    response = _.get(response, 'data', {});
    const message = _.get(response, 'message', 'Update failed')
    if (response.status === "success") {
      successNotification(message);
    } else {
      errorNotification(message);
    }
    await this._getIntitalData();
  }

  CardPanels = (schedulerData) => {
    const items = schedulerData.map(item => {
      const { operation, state, params, eta } = item;

      const _renderDescription = () => {
        const paramsList = [];
        for (const [key, value] of Object.entries(params)) {
          paramsList.push({ key, value });
        }
        const paramsFormatted = paramsList.map(item => {
          if (item.key === 'versioned') {
            return listRow('Type', item.value ? 'Versioned' : 'LKG');
          }
          return listRow(capitalize(item.key), capitalize(item.value));
        })
        return (
          <div>
            {paramsFormatted}
          </div>
        );
      }

      const _renderHeader = () => {
        return (<div>
          <div className="flex-row flex-jc-sb">
            {StatusHoop(state, capitalize(operation.toLowerCase()))}
            {
              <Dropdown item text=''>
                <Dropdown.Menu>
                  <DeleteJob
                    execute={() => this.handleDeleteAction([item.id])}
                    selectedEntities={[item.id]}
                    trigger={< Dropdown.Item text='Delete' />} />
                  {item.state === 'running'
                    ? <UpdateStateJob
                      execute={() => this.handleChangeStateAction([item.id], false)}
                      selectedEntities={[item.id]}
                      trigger={< Dropdown.Item text='Stop' />} />
                    : < Dropdown.Item text='Start' onClick={() => this.handleChangeStateAction([item.id], true)} />}
                </Dropdown.Menu>
              </Dropdown>
            }
          </div>
          <Divider />
        </div>);
      }

      return {
        header: _renderHeader(),
        description: _renderDescription(),
        meta: `Last Executed ${eta}`,
      }
    });
    return (<Card.Group itemsPerRow={4} items={items} />);
  }

  _renderPage = () => {
    const { loading, loadingPercentage, schedulerData } = this.state;
    if (loading) {
      return LoaderProgress(loadingPercentage);
    }
    return (
      <>
        <Segment.Group>
          <Segment className="flex-row flex-jc-sb">
            <ModalAddJob
              refreshTable={this._getIntitalData}
              trigger={<Button icon labelPosition='right' primary>
                <Icon name='add' />
                {`Create ${ENTITY_TABLE}`}
              </Button>} />
            <Button.Group icon>
              <Button>
                <Icon name='play' />
              </Button>
              <Button>
                <Icon name='pause' />
              </Button>
            </Button.Group>
          </Segment>
        </Segment.Group>
        {this.CardPanels(schedulerData)}
      </>
    );
  }

  render() {
    return (this._renderPage());
  }
}

