import React, { Component } from 'react';
import { Button, Message, Form, Modal, Radio, Dropdown, Divider } from 'semantic-ui-react';
import { TransitionModal } from 'semantic-ui-react-transition-modal';
import _ from 'lodash';
import { deployerActionDirect } from '../Utils/APIUtil.js';
import { successNotification, i18n, i18nResolver } from '../Utils/CommonUtil';
import { ModalHeader, StatusHoop, IndefiniteLoader } from './CustomComponents';
import CONSTANTS from '../Utils/Constants';
import { capitalize } from '@material-ui/core';

const { ACTIONS } = CONSTANTS;

const OPERATION_OPTIONS = [
  { key: 'BACKUP', text: 'BACKUP', value: 'BACKUP' },
  { key: 'DEPLOY', text: 'DEPLOY', value: 'DEPLOY' }
];

const STATE_OPTIONS = [
  { key: 'running', text: StatusHoop("running", "Running"), value: 'running' },
  { key: 'stopped', text: StatusHoop("stopped", "Stopped"), value: 'stopped' }
];

export default class ModalAddJob extends Component {
  state = {
    versioned: true,
    loading: true,
    selectedEntity: '',
    open: false,
    loadingtunnelOptions: true,
    schedulerSchema: [],
    errors: []
  }

  handleRadio = (e, { value }) => this.setState({ versioned: value })

  onChange = (e, stateName) => {
    const value = e.target.value;
    let stateObject = { ...this.state };
    let updateObject = {};
    switch (stateName) {
      case 'entity':
        updateObject = {
          [stateName]: value,
          'remote_path': `${value.toUpperCase()}_BACKUP`
        };
        stateObject = _.extend(stateObject, updateObject);
        this.setState(stateObject);
        break;
      default:
        this.setState({
          [stateName]: value,
          errors: []
        });
        break;
    }
  }

  onSelectChange = (value, stateName) => {
    this.setState({
      [stateName]: value,
      errors: []
    });
  }

  setOpen = async () => {
    this.setState({ open: true });
    let schedulerSchema = await deployerActionDirect(ACTIONS.SCHEMA, { table: 'scheduler' });
    let tunnelOptions = await deployerActionDirect(ACTIONS.GET_TUNNELS);
    tunnelOptions = _.get(tunnelOptions, 'data.rows', []);
    schedulerSchema = _.get(schedulerSchema, 'data.columns', []);

    const ignoreFields = [
      'id',
      'timestamp_exec'
    ];
    schedulerSchema = _.remove(schedulerSchema, schema => {
      return !ignoreFields.includes(schema);
    });

    let schemaStates = {};
    // Populate Default fields if any here
    schedulerSchema.forEach(field => {
      let defaultValue = null;
      if (field === 'params') {
        defaultValue = {};
      }
      schemaStates = _.extend(schemaStates, { [field]: defaultValue });

    });

    const stateObject = _.extend(schemaStates, {
      loading: false,
      loadingtunnelOptions: false,
      tunnelOptions,
      schedulerSchema,
      open: true
    });

    this.setState(stateObject);
  }

  setClose = () => {
    this.setState({ open: false });
  }

  handleSubmit = async () => {
    const { schedulerSchema } = this.state;
    let payload = {};
    schedulerSchema.forEach(item => {
      let key = item;
      let value = _.get(this.state, item, '');
      if (item === 'params') {
        const versioned = _.get(this.state, 'versioned', false);
        const entity = _.get(this.state, 'selectedEntity' , '');
        const paramSpec = { entity, versioned };
        value = JSON.stringify(paramSpec);
      }
      payload = _.extend(payload, { [key]: value });
    });
    let response = await deployerActionDirect(ACTIONS.ADD_SCHEDULER, payload);
    response = _.get(response, 'data', {});

    if (response.status === "success") {
      this.setClose();
      this.props.refreshTable();
      successNotification(response.message);
    } else {
      const errors = [];
      errors.push(i18nResolver(_.get(response, 'error', '')));
      this.setState({
        errors
      });
    }
  }

  _inputSectionParams = () => {
    const options = this.state.tunnelOptions.map(item => {
      return { key: item.entity, text: capitalize(item.entity), value: item.entity };
    })
    return (
      <>
        <Divider />
        <label><a href>{'Entities'}</a></label>
        <Dropdown
          placeholder='Select Entity'
          value={this.state.contents}
          fluid
          loading={this.state.loadingtunnelOptions}
          selection
          onChange={(e, { value }) => this.onSelectChange(value, 'selectedEntity')}
          options={options}
        />
        <label className="mt-10 mb-10"><a href>{'Backup Type'}</a></label>
        <Radio
          label='LKG'
          name='radioGroup'
          value={false}
          checked={(this.state.versioned === false)}
          onChange={this.handleRadio}
        />
        <Radio
          className="ml-15"
          label='Versioned'
          name='radioGroup'
          value={true}
          checked={(this.state.versioned === true)}
          onChange={this.handleRadio}
        />
        <Message info className="mt-10">
          {this.state.versioned
            ? <p>This will always create a new verson of backup on job execution.</p>
            : <p>This will overwrite the previously existing LKG Backup on job execution.</p>}
        </Message>
        <Divider />
      </>
    );
  }

  _populateFormFields = () => {
    const { schedulerSchema, loadingtunnelOptions, loading } = this.state;

    if (loading) {
      return IndefiniteLoader();
    }
    const fields = schedulerSchema.map(column => {
      switch (column) {
        case 'operation':
          return (
            <Form.Field>
              <label>{i18n(column)}</label>
              <Dropdown
                placeholder='Select Operation'
                value={this.state.operation}
                fluid
                loading={loadingtunnelOptions}
                selection
                onChange={(e, { value }) => this.onSelectChange(value, 'operation')}
                options={OPERATION_OPTIONS}
              />
            </Form.Field>
          );
        case 'state':
          return (
            <Form.Field>
              <label>{i18n(column)}</label>
              <Dropdown
                placeholder='Select State'
                value={this.state.state}
                fluid
                loading={loadingtunnelOptions}
                selection
                onChange={(e, { value }) => this.onSelectChange(value, 'state')}
                options={STATE_OPTIONS}
              />
            </Form.Field>
          );
        case 'params':
          return (
            <Form.Field>
              <label>{i18n(column)}</label>
              {this._inputSectionParams()}
            </Form.Field>
          );
        default:
          return (
            <Form.Field>
              <label>{i18n(column)}</label>
              <input placeholder={i18n(column)} onChange={(e) => this.onChange(e, column)} value={this.state[column]} />
            </Form.Field>
          );
      }
    });

    return fields;
  }

  render() {
    const { loading } = this.state;
    return (
      <TransitionModal
        className="fullpage-modal"
        closeOnDimmerClick={false}
        animation="fade up"
        duration={200}
        onClose={() => this.setClose()}
        onOpen={() => this.setOpen()}
        open={this.state.open}
        trigger={this.props.trigger}
      >
        <Modal.Header>
          {ModalHeader('Create Job', this.setClose)}
        </Modal.Header>
        <Modal.Content>
          <Modal.Description>
            <div className="add-form-body w-50">
              <Form error>
                <Message
                  className={this.state.errors.length > 0 ? 'alert visible' : 'alert invisible'}
                  error
                  header='Failed'
                  content={this.state.errors.join('. ')}
                />
                {this._populateFormFields()}
              </Form>
            </div>
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button onClick={() => this.setClose()}>
            Cancel
          </Button>
          <Button
            disabled={loading}
            content="Submit"
            onClick={() => this.handleSubmit()}
            positive
          />
        </Modal.Actions>
      </TransitionModal>
    )
  }
}
