import React, { Component } from 'react'
import _ from 'lodash';
import {
    Divider,
    Menu,
    Dropdown,
    Icon
} from 'semantic-ui-react'
import {
    unsetLogin, refreshPage,
    getLogin, unsetInstanceURL
} from '../Utils/CommonUtil';
import { deployerActionDirect } from '../Utils/APIUtil.js';
import { taskRow, StatusHoop } from './CustomComponents';
import Logo from '../Images/logo.svg';
import AppTitle from './AppTitle';
import CONSTANTS from '../Utils/Constants';

const { ACTIONS } = CONSTANTS;
const { username } = getLogin();

export default class NavigationBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            activeItem: 'home',
            logsData: [],
        }
    }


    async componentDidMount() {
        await this._getIntitalData();
    }

    _getIntitalData = async () => {
        let logsData = await deployerActionDirect(ACTIONS.GET_LOGS);
        logsData = _.get(logsData, 'data.rows', []);

        this.setState({
            loading: false,
            logsData,
        });
    }

    handleItemClick = (e, { name }) => {
        this.setState({ activeItem: name });
        if (name === 'logout') {
            unsetLogin();
            this.props.setAppLoader(true);
            refreshPage();
        }
        if (name === 'disconnect') {
            unsetLogin();
            unsetInstanceURL();
            this.props.setAppLoader(true);
            refreshPage();
        }
    }

    render() {
        const { logsData } = this.state;
        return (
            <Menu secondary className="navigation-bar">
                <Menu.Item>
                    <img style={{ height: 45, width: 45 }} alt="Message" src={Logo} />
                </Menu.Item>
                <Menu.Item>
                    <AppTitle size={1.5} />
                </Menu.Item>
                <Menu.Menu position='right'>
                    <Dropdown pointing="right top" scrolling={true} item text={<div>{StatusHoop('success', <Icon name='bell outline' />)}</div>}>
                        <Dropdown.Menu>
                            {logsData.map((log, i) => {
                                return <Dropdown.Item key={i}>{taskRow(log.step, log.source, log.status, log.eta)}</Dropdown.Item>;
                            })}
                            <Divider />
                            <Dropdown.Item name="navigate-activities" text={<div><a href={false}>{'View Details'}</a></div>} onClick={this.handleItemClick} />
                        </Dropdown.Menu>
                    </Dropdown>
                    <Dropdown item text={<div><Icon name='user outline' /> <span>{username}</span></div>}>
                        <Dropdown.Menu>
                            <Dropdown.Item icon='setting' text='Settings' />
                            <Dropdown.Item icon='unlinkify' name="disconnect" text='Disconnect' onClick={this.handleItemClick} />
                            <Dropdown.Item icon='shutdown' name="logout" text='Logout' onClick={this.handleItemClick} />
                        </Dropdown.Menu>
                    </Dropdown>
                </Menu.Menu>
            </Menu>
        )
    }
}
