import React, { Component } from 'react';
import { Dropdown, Icon, Menu } from 'semantic-ui-react';
import { capitalize, getcurrentPage, setcurrentPage, setLoadingMessageIndex } from '../Utils/CommonUtil';
import CONSTANT from '../Utils/Constants';
import Dashboard from './Dashboard';
import Tunnels from './Tunnels';
import Activities from './Activities';
import DeployWizard from './DeployWizard';
import DeployLKG from './DeployLKG';
import BackupLKG from './BackupLKG';
import Scheduler from './Scheduler';
import BackupsExplorer from './BackupsExplorer';

const { PAGES } = CONSTANT;

export default class MenuComponent extends Component {
    state = {
        activeItem: getcurrentPage() || PAGES.DASHBOARD
    }

    handleItemClick = (e, { name }) => this.setState({ activeItem: name })
    _renderPage = (pageName) => {
        setcurrentPage(pageName);
        setLoadingMessageIndex();
        switch (pageName) {
            case PAGES.DASHBOARD:
                return <Dashboard />
            case PAGES.WIZARD_DEPLOY:
                return <DeployWizard />
            case PAGES.LKG_DEPLOY:
                return <DeployLKG />
            case PAGES.LKG_BACKUP:
                return <BackupLKG refetchNotification={this.props.refetchNotification} />
            case PAGES.TUNNELS:
                return <Tunnels />
            case PAGES.ACTIVITIES:
                return <Activities />
            case PAGES.SCHEDULER:
                return <Scheduler />
            case PAGES.EXPLORER:
                return <BackupsExplorer />
            default:
                return <div />
        }
    }
    render() {
        const { activeItem } = this.state

        return (
            <div className="content-panel">
                <Menu className="menu-vertical-home" vertical>
                    <Menu.Item
                        name={PAGES.DASHBOARD}
                        active={activeItem === PAGES.DASHBOARD}
                        onClick={this.handleItemClick}
                    >
                        <Icon name='grid layout' />
                        {capitalize(PAGES.DASHBOARD)}
                    </Menu.Item>

                    <Menu.Item
                        name={PAGES.TUNNELS}
                        active={activeItem === PAGES.TUNNELS}
                        onClick={this.handleItemClick}
                    >
                        <Icon name='exchange' />
                        {capitalize(PAGES.TUNNELS)}
                    </Menu.Item>

                    <Menu.Item>
                        Deploy
                        <Menu.Menu>
                            <Menu.Item
                                name={PAGES.WIZARD_DEPLOY}
                                active={activeItem === PAGES.WIZARD_DEPLOY}
                                onClick={this.handleItemClick}
                            >
                                {capitalize(PAGES.WIZARD)}
                            </Menu.Item>
                            <Menu.Item
                                name={PAGES.LKG_DEPLOY}
                                active={activeItem === PAGES.LKG_DEPLOY}
                                onClick={this.handleItemClick}
                            >
                                {(PAGES.LKG).toLocaleUpperCase()}
                            </Menu.Item>
                        </Menu.Menu>
                    </Menu.Item>

                    <Menu.Item>
                        Backup
                        <Menu.Menu>
                            <Menu.Item
                                name={PAGES.LKG_BACKUP}
                                active={activeItem === PAGES.LKG_BACKUP}
                                onClick={this.handleItemClick}
                            >
                                {(PAGES.LKG).toLocaleUpperCase()}
                            </Menu.Item>
                            <Menu.Item
                                name={PAGES.EXPLORER}
                                active={activeItem === PAGES.EXPLORER}
                                onClick={this.handleItemClick}
                            >
                                {capitalize(PAGES.EXPLORER)}
                            </Menu.Item>
                        </Menu.Menu>
                    </Menu.Item>

                    <Menu.Item
                        name={PAGES.SCHEDULER}
                        active={activeItem === PAGES.SCHEDULER}
                        onClick={this.handleItemClick}
                    >
                        <Icon name='history' />
                        {capitalize(PAGES.SCHEDULER)}
                    </Menu.Item>

                    <Menu.Item
                        name={PAGES.ACTIVITIES}
                        active={activeItem === PAGES.ACTIVITIES}
                        onClick={this.handleItemClick}
                    >
                        <Icon name='list' />
                        {capitalize(PAGES.ACTIVITIES)}
                    </Menu.Item>

                    <Dropdown item text='More'>
                        <Dropdown.Menu>
                            <Dropdown.Item icon='edit' text='Edit Profile' />
                            <Dropdown.Item icon='globe' text='Choose Language' />
                            <Dropdown.Item icon='settings' text='Account Settings' />
                        </Dropdown.Menu>
                    </Dropdown>

                </Menu>
                <div className="content-section">
                    {this._renderPage(activeItem)}
                </div>
            </div>
        )
    }
}