import React from 'react'
import { Button, Modal, Message } from 'semantic-ui-react';
import { TransitionModal } from 'semantic-ui-react-transition-modal';

export default function DeleteTunnel({ trigger, execute, selectedEntities }) {
    const [open, setOpen] = React.useState(false)

    return (
        <TransitionModal
            closeOnDimmerClick={false}
            animation="fade up"
            duration={200}
            onClose={() => setOpen(false)}
            onOpen={() => setOpen(true)}
            open={open}
            trigger={trigger}
        >
            <Modal.Header>Delete Tunnel</Modal.Header>
            <Modal.Content image>
                <Modal.Description>
                    <Message warning>
                        <Message.Header>You must register before you can do that!</Message.Header>
                        <p>Visit our registration page, then try again.</p>
                    </Message>
                    <p>Are you sure you want delete <b>{selectedEntities.join(", ")}</b> ?</p>
                </Modal.Description>
            </Modal.Content>
            <Modal.Actions>
                <Button onClick={() => setOpen(false)}>Cancel</Button>
                <Button
                    onClick={() => {
                        setOpen(false);
                        execute();
                    }}
                    negative
                >
                    Delete
                </Button>
            </Modal.Actions>
        </TransitionModal>
    )
}
