import _ from 'lodash';
import BaseConstants from './Constants.js';
import { store } from 'react-notifications-component';

export const deleteAllKeys = () => {
    localStorage.clear();
}

export const getInstanceEndpoint = () => {
    const url = getInstanceURL();
    const temp = url.split('/');
    temp.pop();
    return temp.join('/');
}

export const getHostingDomain = () => {
    const url = getInstanceURL();
    const { hostname } = new URL(url);
    return hostname;
}

export const getInstanceURL = () => {
    return localStorage.getItem(BaseConstants.INSTANCE_URL);
}

export const setInstanceURL = (url) => {
    return localStorage.setItem(BaseConstants.INSTANCE_URL, url);
}

export const unsetInstanceURL = () => {
    return localStorage.removeItem(BaseConstants.INSTANCE_URL);
}

export const setcurrentPage = (pageName) => {
    return localStorage.setItem(BaseConstants.CURRENT_PAGE, pageName);
}

export const getcurrentPage = () => {
    return localStorage.getItem(BaseConstants.CURRENT_PAGE);
}

export const setLoadingMessageIndex = () => {
    const randomNumber = _.random(0, BaseConstants.BUSY_MSGS.length);
    return localStorage.setItem(BaseConstants.BUSY_MSG_INDEX, randomNumber);
}

export const getLoadingMessageIndex = () => {
    return localStorage.getItem(BaseConstants.BUSY_MSG_INDEX);
}

export const setLogin = (username, password) => {
    localStorage.setItem(BaseConstants.USERNAME, username);
    localStorage.setItem(BaseConstants.PASSWORD, password);
}

export const getLogin = () => {
    return {
        username: localStorage.getItem(BaseConstants.USERNAME),
        password: localStorage.getItem(BaseConstants.PASSWORD)
    }
}

export const unsetLogin = () => {
    localStorage.removeItem(BaseConstants.USERNAME);
    localStorage.removeItem(BaseConstants.PASSWORD);
}

export const refreshPage = () => {
    window.location.reload();
}

export const i18n = (key) => {
    if (BaseConstants.I18N.hasOwnProperty(key)) {
        return BaseConstants.I18N[key];
    }
    return key;
}

export const i18nResolver = (str) => {
    const words = str.split(" ");
    const wordsTranslated = [];
    words.forEach(word => {
        wordsTranslated.push(i18n(word));
    });
    return wordsTranslated.join(" ");
}


export const showBusyMessage = () => {
    const randomNumber = getLoadingMessageIndex() || 0;
    return BaseConstants.BUSY_MSGS[randomNumber];
}

export const getRandomName = (prefixNumber = true) => {
    if (prefixNumber) {
        const randomdigits = (Math.floor(Math.random() * 10000) + 10000).toString().substring(1);
        return `${BaseConstants.GREEK_GODS[Math.floor((Math.random() * (BaseConstants.GREEK_GODS.length - 1)) + 0)]}_${randomdigits}`.toLowerCase();
    }
    return `${BaseConstants.GREEK_GODS[Math.floor((Math.random() * (BaseConstants.GREEK_GODS.length - 1)) + 0)]}`.toLowerCase();
}

export const capitalize = (string) => {
    let ret = string;
    if (!string || string.length === 0) {
        return string;
    }
    try {
        ret = string.charAt(0).toUpperCase() + string.slice(1);
    } catch (e) { }
    return ret;
}

export const shortETA = (eta) => {
    if (eta) {
        const etaArr = eta.split(' ');
        eta = (etaArr[0] || '-') + (etaArr[1][0] || '-') + ' ' + (etaArr[2] || '-');
    } else {
        eta = 'Just now';
    }
    return eta;
}

export const trimChar = (string, charToRemove) => {
    while (string.charAt(0) === charToRemove) {
        string = string.substring(1);
    }

    while (string.charAt(string.length - 1) === charToRemove) {
        string = string.substring(0, string.length - 1);
    }

    return string;
}

export const successNotification = (message = "", title = "Success", duration = 5) => {
    // convert to seconds
    duration = duration * 1000;

    store.addNotification({
        title,
        message: message,
        type: "success",
        insert: "top",
        container: "bottom-right",
        animationIn: ['animated fadeIn'],
        animationOut: ['animated fadeOut'],
        dismiss: {
            duration,
            onScreen: false
        },
        slidingExit: {
            delay: 300
        }
    });
}

export const errorNotification = (message = "", title = "Failed") => {
    store.addNotification({
        title,
        message: message,
        type: "danger",
        insert: "top",
        container: "bottom-right",
        animationIn: ['animated fadeIn'],
        animationOut: ['animated fadeOut'],
        dismiss: {
            duration: 5000,
            onScreen: false
        },
        slidingExit: {
            delay: 300
        }
    });
}

export function updateLoadingProgress(loadingPercentage) {
    this.setState({
        loadingPercentage
    });
}

export function progressContinue() {
    let { loadingPercentage } = { ...this.state };
    loadingPercentage = loadingPercentage + 0.1;
    this.setState({
        loadingPercentage
    });
}
