import React, { Component } from 'react';
import _ from 'lodash';
import {
  Icon,
  Header,
  Grid,
  Divider,
  Statistic,
  Card,
  Label,
  List,
} from 'semantic-ui-react'
import { Doughnut } from 'react-chartjs-2';
import 'semantic-ui-css/semantic.min.css'
import { deployerActionDirect } from '../Utils/APIUtil.js';
import { capitalize, getHostingDomain, getInstanceEndpoint,
  updateLoadingProgress, progressContinue } from '../Utils/CommonUtil';
import CONSTANTS from '../Utils/Constants';
import {
  LoaderProgress,
  PulseIndicator,
  listRow,
  StatusHoop,
  CardHeader
} from './CustomComponents';

const { ACTIONS } = CONSTANTS;

class Dashboard extends Component {
  constructor(props) {
    super(props);

    this.constants = {
      storageThreshold: 50
    }

    this.state = {
      loadingPercentage: 0,
      loading: true,
      backupData: {},
      storageData: {},
      dbData: {}
    }
    this.updateLoadingProgress = updateLoadingProgress.bind(this);
    this.progressContinue = progressContinue.bind(this);
  }




  async componentDidMount() {
    this.timer = setInterval(() => this.progressContinue(), 50);
    let backupData = await deployerActionDirect(ACTIONS.BACKUP_STATUS);
    this.updateLoadingProgress(35);
    let storageData = await deployerActionDirect(ACTIONS.STORAGE_STATUS);
    this.updateLoadingProgress(60);
    let dbData = await deployerActionDirect(ACTIONS.DB_STATUS);
    this.updateLoadingProgress(100);
    clearInterval(this.timer);

    backupData = _.get(backupData, 'data.data', {});
    storageData = _.get(storageData, 'data', {});
    dbData = _.get(dbData, 'data', {});

    this.setState({
      loading: false,
      backupData,
      storageData,
      dbData
    });
  }


  componentWillUnmount() {
    clearInterval(this.timer);
  }

  healthPanel = (compositeObject) => {
    const { storageData } = compositeObject;
    const { storageThreshold } = this.constants;

    const alerts = [];
    let storageStatus = true;

    // Health Checks
    // Storage within 50% usage
    if (storageData.percent_used > storageThreshold) {
      alerts.push(`Storage used beyond ${storageThreshold}%`);
      storageStatus = "warning";
    }

    const status = alerts.length > 0 ? 'warning' : 'good';
    const alertsItem = alerts.map(item => {
      return <div className="flex-row pt-10">
        <Icon className="custom-icon yellow" name='warning circle' />
        <div>{item}</div>
      </div>;
    })

    const _renderList = () => {
      return (
        <List>
          <List.Item>
            {/* <List.Header>Monitoring</List.Header> */}
            {listRow('Cluster', StatusHoop(true))}
            {listRow('Storage', StatusHoop(storageStatus))}
            {listRow('Parity', StatusHoop(true))}
          </List.Item>
          <Divider />
          <List.Item>
            <List.Header>Alerts</List.Header>
            {
              alerts.length > 0
                ? alertsItem
                : <div className="flex-row flex-jc-c mt-50">
                  <div>
                    <Icon className="custom-icon gray" name='bell outline' size='huge' />
                    <div className="mt-10">No Alerts</div>
                  </div>
                </div>}
          </List.Item>

        </List>
      );
    }

    return (
      <Card fluid>
        <Card.Content className="flex-grow-unset" header={CardHeader('Health')} />
        <Card.Content
          description={
            <div>
              <div className="flex-row flex-jc-c">
                {PulseIndicator(status)}
              </div>
              <Divider />
              {_renderList()}
            </div>
          }
        />
      </Card>

    );
  }

  dropboxPanel = () => {
    const { storageData } = this.state;
    const { type, username, percent_available,
      percent_used, email, used_space, total_space,
      filesData } = storageData;
    const allFiles = _.get(filesData, 'files', []);

    const data = {
      labels: ['Used', 'Available'],
      datasets: [
        {
          label: '# of Votes',
          data: [percent_used, percent_available],
          backgroundColor: [
            'rgba(75, 192, 192, 0.2)',
            'rgba(153, 102, 255, 0.2)'
          ],
          borderColor: [
            'rgba(75, 192, 192, 1)',
            'rgba(153, 102, 255, 1)'
          ],
          borderWidth: 1
        },
      ],
    };

    const options = {
      plugins: {
        legend: false
      }
    };

    const _listRow = (title, text) => {
      return (
        <div className="list-item-row">
          <div>{title}</div>
          <div>{text}</div>
        </div>);
    }

    const _renderList = () => {
      return (
        <List>
          <List.Item>
            <List.Header>
              <div className="flex-row flex-jc-sb w-100">
                <div>Account</div>
                <div>
                  <a
                    rel="noopener noreferrer"
                    target="_blank"
                    href={`https://www.dropbox.com/login?email=${email}`}>Login</a>
                </div>
              </div>
            </List.Header>
            {_listRow('Email', email)}
            {_listRow('Username', username)}
            {_listRow('Type', capitalize(type))}
          </List.Item>
          <Divider />
          <List.Item>
            <List.Header>Stats</List.Header>
            {_listRow('Used/Available', `${used_space}/${total_space}`)}
          </List.Item>
          <Divider />
          <List.Item>
            <List.Header>File System</List.Header>
            {_listRow('Files Hosted', allFiles.length)}
          </List.Item>
        </List>
      );
    }

    return (
      <Card fluid>
        <Card.Content className="flex-grow-unset" header={CardHeader('Storage')} />
        <Card.Content
          description={
            <div>
              <div className="card-chart-container">
                <Doughnut
                  options={options}
                  data={data} />
              </div>
              <div>{`Available ${percent_available}%`}</div>
              <Divider />
              {_renderList()}
            </div>
          }
        />
      </Card>

    );
  }

  hostingPanel = () => {
    const { dbData } = this.state;
    const { dbReference, dbList } = dbData;
    const allDBs = _.get(dbList, 'rows', []);
    const creds = _.get(dbReference, 'credentials', []);

    const _listRow = (title, text) => {
      return (
        <div className="list-item-row">
          <div>{title}</div>
          <div>{text}</div>
        </div>);
    }

    const _renderList = () => {
      return (
        <List>
          <List.Item>
            <List.Header>
              <div className="flex-row flex-jc-sb w-100">
                <div>Database</div>
                <div>
                  <a
                    target="_blank"
                    rel="noopener noreferrer"
                    href={`${getInstanceEndpoint()}/adminer.php?username=${creds.username}&db=${creds.database}`}>
                    Login
                  </a>
                </div>
              </div>
            </List.Header>
            {_listRow('Host', creds.host)}
            {_listRow('Deployer Database', creds.database)}
            {_listRow('Username', creds.username)}
            {_listRow('Password', creds.password)}
            {_listRow('Databases', allDBs.length)}
            {_listRow('Tables', allDBs.length)}
          </List.Item>
        </List>
      );
    }

    return (
      <Card fluid>
        <Card.Content className="flex-grow-unset" header={CardHeader('Hosting')} />
        <Card.Content
          description={
            <div>
              <div className="hosting-name flex-row">
                <div className="domain">
                  {(getHostingDomain()).split('.')[0]}
                </div>
                <div className="ext">
                  {`.${(getHostingDomain()).split('.')[1]}`}
                </div>
              </div>
              <Divider />
              {_renderList()}
            </div>
          }
        />
      </Card>

    );
  }

  CardPanels = () => {
    const { el, versioned, lkg } = this._statsBackup();
    const items = [
      {
        header: <Header className="header-with-label" size='medium'>Backups
          <Label>
            {lkg}
          </Label>
        </Header>,
        description: el,
        meta: 'Created on storage',
        key: 1
      },
      {
        header: <Header className="header-with-label" size='medium'>LKG Builds <Label>
          {lkg}
        </Label></Header>,
        description: this._statsLKG(),
        meta: '',
        key: 2
      },
      {
        header: <Header className="header-with-label" size='medium'>Versioned Builds <Label>
          {versioned}
        </Label></Header>,
        description: this._statsVersioned(),
        meta: '',
        key: 3
      },
    ];
    return (<Card.Group className="dashboard-cards-group" items={items} />);
  }

  _statsBackup = () => {
    const { backupData } = this.state;
    let allBackups = [];
    let versionedBackups = [];
    let lkgBackups = [];
    for (const [, value] of Object.entries(backupData)) {
      allBackups = allBackups.concat(value);
      const buildListFilteredVersioned = value.filter(item => {
        return item.name.includes('.FN.');
      }) || [];
      const buildListFilteredLKG = value.filter(item => {
        return !item.name.includes('.FN.');
      }) || [];
      versionedBackups = versionedBackups.concat(buildListFilteredVersioned);
      lkgBackups = lkgBackups.concat(buildListFilteredLKG);
    }

    const el = (
      <Statistic.Group horizontal>
        <Statistic>
          <Statistic.Value>{allBackups.length}</Statistic.Value>
          <Statistic.Label>Total</Statistic.Label>
        </Statistic>
      </Statistic.Group>);

    return ({
      el,
      versioned: versionedBackups.length,
      lkg: lkgBackups.length,
      all: allBackups.length
    });
  }

  _statsVersioned = () => {
    const { backupData } = this.state;
    let versionedBackupObject = [];

    for (const [key, value] of Object.entries(backupData)) {
      const buildListFiltered = value.filter(item => {
        return item.name.includes('.FN.');
      }) || [];
      versionedBackupObject.push(
        {
          dirName: key,
          lastBackup: _.get(_.last(buildListFiltered), 'eta', '-')
        }
      );
    }

    return (
      <List divided relaxed>
        {versionedBackupObject.map(item => {
          return (<List.Item key={item.dirName}>
            <List.Icon name='hdd outline' size='large' verticalAlign='middle' />
            <List.Content>
              <List.Header as='a'>{item.dirName}</List.Header>
              <List.Description as='a'>{item.lastBackup}</List.Description>
            </List.Content>
          </List.Item>);
        })}
      </List>
    );
  }

  _statsLKG = () => {
    const { backupData } = this.state;
    let versionedBackupObject = [];
    for (const [key, value] of Object.entries(backupData)) {
      const buildListFiltered = value.filter(item => {
        return !item.name.includes('.FN.');
      }) || [];
      versionedBackupObject.push(
        {
          dirName: key,
          lastBackup: _.get(_.last(buildListFiltered), 'eta', '-')
        }
      );
    }

    return (
      <List divided relaxed>
        {versionedBackupObject.map(item => {
          return (<List.Item key={item.dirName} >
            <List.Icon name='code branch' size='large' verticalAlign='middle' />
            <List.Content>
              <List.Header as='a'>{item.dirName}</List.Header>
              <List.Description as='a'>{item.lastBackup}</List.Description>
            </List.Content>
          </List.Item>);
        })}
      </List>
    );
  }

  _renderPage = () => {
    const { loading, loadingPercentage, storageData } = this.state;
    const compositeObject = {
      storageData
    }

    if (loading) {
      return LoaderProgress(loadingPercentage);
    }

    return (
      <Grid className="dashboard-grid-layout" stretched columns={4}>
        <Grid.Row>
          <Grid.Column>
            {this.healthPanel(compositeObject)}
          </Grid.Column>
          <Grid.Column>
            {this.dropboxPanel()}
          </Grid.Column>
          <Grid.Column>
            {this.CardPanels()}
          </Grid.Column>
          <Grid.Column>
            {this.hostingPanel()}
          </Grid.Column>
        </Grid.Row>
      </Grid>
    );
  }

  render() {
    return (this._renderPage());
  }
}

export default Dashboard;

