import React, { Component } from 'react';
import _ from 'lodash';
import {
  Checkbox,
  Icon,
  Segment,
  Header,
  Message,
  Dropdown,
  Button
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'
import { deployerActionDirect } from '../Utils/APIUtil.js';
import CONSTANTS from '../Utils/Constants';
import { capitalize } from '../Utils/CommonUtil.js';
import { parseBuildInfo } from './CustomComponents';

const { ACTIONS } = CONSTANTS;

class BackupLKG extends Component {
  constructor(props) {
    super(props);

    this.state = {
      versioned: true,
      selectedEntity: '',
      backupResponse: {
        text: 'We are fetching that content for you.',
        title: 'Just one second',
        isPositive: false
      },
      loading: true,
      backupData: {},
      tunnelsData: []
    }
  }

  onSelectChange = (value, stateName) => {
    this.setState({ [stateName]: value });
  }

  toggle = () => this.setState((prevState) => ({ versioned: !prevState.versioned }))

  handleBackup = async (selectedEntity, versioned) => {
    this.setState({
      backupResponse: {
        text: 'Please wait while the backup is being created.',
        title: 'Backup In Progress',
        isPositive: true
      },
      loading: true
    });

    let response = await deployerActionDirect(ACTIONS.BACKUP_ENTITY, { entity: selectedEntity, versioned });
    response = _.get(response, 'data', {});
    let { logs, message, status } = response;
    logs = logs || ['Oops! Something went wrong.'];

    const backupResponse = {
      text: logs.join('. '),
      title: message,
      isPositive: (status === 'success')
    };

    this.setState({
      loading: false,
      backupResponse
    });

    this.props.refetchNotification();
  }

  async componentDidMount() {
    let backupData = await deployerActionDirect(ACTIONS.BACKUP_STATUS);
    let tunnelsData = await deployerActionDirect(ACTIONS.GET_TUNNELS);

    backupData = _.get(backupData, 'data', {});
    tunnelsData = _.get(tunnelsData, 'data.rows', []);

    this.setState({
      loading: false,
      backupData,
      tunnelsData,
      backupResponse: {
        text: 'We are ready to backup the builds.',
        title: 'Ready',
        isPositive: true
      }
    });
  }

  _renderPage = () => {
    const { backupData, selectedEntity, loading,
      backupResponse, versioned, tunnelsData } = this.state;
    let _renderValidationMessage = <div />;
    const isEntitySelected = (selectedEntity !== '');

    const entityOptions = tunnelsData.map((item, index) => {
      return { key: (index + 1), text: `Backup ${capitalize(item.entity)}`, value: item.entity };
    });

    const entityDetails = _.find(tunnelsData, tunnel => {
      return (tunnel.entity === selectedEntity);
    }) || {};
    const remotePath = _.get(entityDetails, 'remote_path', '-');
    const entityName = _.get(entityDetails, 'entity', '-');

    let entiyBuildData = _.get(backupData, `data.${remotePath}`, []);
    if (versioned) {
      entiyBuildData = _.filter(entiyBuildData, buildItem => {
        return buildItem.name.includes('.FN.');
      });
    } else {
      entiyBuildData = _.filter(entiyBuildData, buildItem => {
        return !buildItem.name.includes('.FN.');
      });
    }
    entiyBuildData = _.last(entiyBuildData) || {};
    const isBuildPresent = (_.get(entiyBuildData, 'path', null) !== null);

    _renderValidationMessage = parseBuildInfo(`${capitalize(entityName)} ${versioned ? 'Versioned' : ''} Backup Information`, entiyBuildData, isBuildPresent, isEntitySelected, backupData.source);

    return (
      <Segment.Group>
        <Header as='h5' attached='top'>
          <div>Backup LKG/Versioned</div>
        </Header>
        <Segment>
          <Message>
            <Message.Header>What does it do?</Message.Header>
            <p>This workflow creates a backup of the last known good (deployed) entitites.</p>
          </Message>
        </Segment>
        <Segment>
          <Dropdown
            disabled={loading}
            onChange={(e, { value }) => this.onSelectChange(value, 'selectedEntity')}
            options={entityOptions}
            placeholder='Choose an option'
            selection
            value={selectedEntity}
          />
          <Checkbox
            onChange={this.toggle}
            checked={this.state.versioned}
            className="backup-version-cbox"
            label='Versioned' />
        </Segment>
        <Segment>
          {_renderValidationMessage}
        </Segment>
        <Segment>
          <Button
            onClick={() => this.handleBackup(selectedEntity, versioned)}
            disabled={loading}
            loading={loading}
            primary>Backup</Button>
          <Message icon positive={backupResponse.isPositive && !loading} negative={!backupResponse.isPositive && !loading}>
            <Icon name='circle notched' loading={loading} />
            <Message.Content>
              <Message.Header>{backupResponse.title}</Message.Header>
              {backupResponse.text}
            </Message.Content>
          </Message>
        </Segment>
      </Segment.Group>
    );
  }

  render() {
    return (this._renderPage());
  }
}

export default BackupLKG;

