import React, { Component } from 'react';
import BaseConstants from '../Utils/Constants.js';


export default class AppTitle extends Component {
    _renderHTML = () => {
        const name = (BaseConstants.APP_NAME).split(' ');
        return (
            <div style={{ fontSize: `${this.props.size}em` }} className="app-name-title">
                <span>{name[0]} </span>
                <span>{name[1]}</span>
            </div>
        );
    }
    render() {
        return (this._renderHTML());
    }
}
