import React from 'react';
import {
    Progress,
    Popup,
    Divider,
    Dimmer,
    Loader,
    Button,
    Segment,
    Grid,
    List,
    Message,
    Card,
    Icon,
} from 'semantic-ui-react'
import { showBusyMessage, capitalize, shortETA } from '../Utils/CommonUtil';
import _ from 'lodash';

export const StatusIcon = (status, textComponent = null) => {
    let colorClass = 'gray-bg';
    switch (status) {
        case true:
            colorClass = 'green-bg';
            break;
        case false:
            colorClass = 'red-bg';
            break;
        default:
            colorClass = 'gray-bg';
            break;
    }

    if (textComponent) {
        if (textComponent === 'auto') {
            textComponent = status ? 'Up' : 'Down';
        }
        return (<div className="flex-row">
            <div className={`status-icon ${colorClass}`}>
            </div>
            <div className="status-text-item">{textComponent}</div>
        </div>);
    }

    return <div className={`status-icon ${colorClass}`}></div>;
}

export const PulseIndicator = (status = false) => {
    let bgColor = 'green-bg';
    switch (status) {
        case true:
        case "success":
        case "good":
            bgColor = 'green-bg';
            break;
        case false:
        case "failed":
            bgColor = 'red-bg';
            break;
        case "warning":
            bgColor = 'yellow-bg';
            break;
        default:
            bgColor = 'gray-bg';
            break;
    }
    return (
        <div className="pulse-box">
            <div className={`pulse-css ${bgColor}`}></div>
        </div>
    );
}

export const StatusHoop = (status, textComponent = null) => {
    let colorClass = 'gray-border';
    switch (status) {
        case 'success':
        case true:
            colorClass = 'green-border';
            break;
        case 'progress':
        case 'running':
            colorClass = 'blue-border blink';
            break;
        case false:
        case 'failed':
        case 'stopped':
            colorClass = 'red-border';
            break;
        case 'warning':
            colorClass = 'yellow-border';
            break;
        default:
            colorClass = 'gray-border';
            break;
    }

    if (textComponent) {
        if (textComponent === 'auto') {
            textComponent = status ? 'Up' : 'Down';
        }
        return (<div className="flex-row">
            <div className={`status-icon-med ${colorClass}`}>
            </div>
            <div className="status-text-item">{textComponent}</div>
        </div>);
    }

    return <div className={`status-icon-med ${colorClass}`}></div>;
}

export const parseBuildInfo = (title, data, isPositive, showInfo = false, source = 'domain@gmail.com') => {
    const infoAvailable = [];
    if (isPositive) {
        infoAvailable.push({ field: 'Verified: ', value: <Icon name='check circle green' /> });
    }
    if (!isPositive) {
        infoAvailable.push({ field: 'Verified: ', value: <Icon name='warning circle yellow' /> });
    }
    if (data.eta) {
        infoAvailable.push({ field: 'Created: ', value: data.eta });
    }
    if (data.path) {
        infoAvailable.push({ field: 'Path: ', value: `Dropbox[${source}]: ${data.path}` });
    }
    if (data.server_modified) {
        infoAvailable.push({ field: 'Last Modified: ', value: data.server_modified });
    }
    if (data.size) {
        infoAvailable.push({ field: 'Size: ', value: data.size });
    }
    if (data.tag) {
        infoAvailable.push({ field: 'Tag: ', value: data.tag });
    }

    if (!showInfo) {
        return (
            <div className="deploy-wizard-message-container">
                <Message
                    icon='info circle'
                    header='Build version'
                    content='Select a build version from the dropdown for deployment.'
                />
            </div>);
    }

    return (
        <div className="deploy-wizard-message-container">
            <List>
                <List.Item>
                    <List.Content>
                        <List.Header as='a'>{title}</List.Header>
                        <br />
                        {infoAvailable.map(item => {
                            return (
                                <List.Description className="path-info-list-desc">
                                    <a href>{item.field}</a>
                                    {item.value}
                                </List.Description>
                            );
                        })}
                    </List.Content>
                </List.Item>
            </List>
        </div>
    );
}

export const extraItemsPopup = (itemsArr, suffix = 'More') => {
    if (itemsArr.length < 1) {
        return '';
    }
    const firstItem = itemsArr[0];
    if (itemsArr.length === 1) {
        return firstItem;
    }
    itemsArr.pop();
    const remainingItems = itemsArr;
    const remainingItemsEl = remainingItems.map(item => {
        return <div>{item}</div>;
    });
    remainingItemsEl.unshift(<Divider />);

    return (<Popup
        content={<div>{remainingItemsEl}</div>}
        key={'Details'}
        header={'Details'}
        trigger={<div className="flex-row cursor-link">
            <div>{firstItem}</div>
            <div>&nbsp;<a href>{`+${remainingItems.length} ${suffix}`}</a></div>
        </div>}
    />);
}

export const listRow = (title, text) => {
    return (
        <div className="list-item-row">
            <div><b>{title}</b></div>
            <div>{text}</div>
        </div>);
}

export const taskRow = (step, source, status, eta) => {
    eta = shortETA(eta);
    return (
        <div className="list-item-row task-row">
            <div className="flex-row flex-jc-sb w-100">
                {StatusHoop(status, <b>{step}</b>)}
                <div className="flex-row">
                    <div>{capitalize(source)}</div>
                    <div className="task-list-eta">{eta}</div>
                </div>
            </div>
        </div>);
}

export const loaderCustom = () => {
    return (<Dimmer active inverted>
        <Loader content='Loading' />
    </Dimmer>);
}

export const LoaderProgress = (percent) => {
    const message = showBusyMessage();
    return (
        <div className="loader-progress-bar">
            <Progress percent={percent} color="blue" size='tiny'>
                {message}
            </Progress>
        </div>);
}

export const IndefiniteLoader = () => {
    return (<div className="flex-row w-100 flex-jc-c">
        {<Icon name='circle notched blue' loading={true} />}
        {showBusyMessage()}
    </div>);
}

export const ModalHeader = (title, closeFunction = null) => {
    return (
        <div className="flex-row flex-jc-sb">
            <div className="title-text">{title}</div>
            <Icon className="modal-close-btn" onClick={closeFunction} name='close' />
        </div>
    );
}

export const CardHeader = (title, closeFunction = null) => {
    return (
        <div className="flex-row flex-jc-sb">
            <div className="title-card-text">{title}</div>
            <Icon className="modal-close-btn" onClick={closeFunction} name='close' />
        </div>
    );
}


export const truncateIt = (text, limit) => {
    if (!text) {
        return text;
    }

    const truncate = (text) => text.length > limit ? `${text.substring(0, limit)}...` : text;
    return (<Popup
        trigger={<a href>{truncate(text)}</a>}
        content={text}
        inverted />
    );
}

export const serviceDownSegment = (compositeObject) => {
    const { dbHealth, storageHealth, onClickRetry, onClickDisconnect } = compositeObject;
    const isDatabaseUP = (_.get(dbHealth, 'status', 'failed') === 'success');
    const isStorageUP = (_.get(storageHealth, 'status', 'failed') === 'success');
    const dbRef = _.get(dbHealth, 'dbReference.credentials', {});
    const dbError = _.get(dbHealth, 'dbReference.error', 'Database not configured');
    const storageRef = _.get(storageHealth, 'storageReference.credentials', {});
    const storageError = _.get(storageHealth, 'storageReference.error', 'Storage not configured');

    return (
        <Segment.Group>
            <Segment>
                <Message negative>
                    <Message.Header>Services Down</Message.Header>
                    <p>One of more services required by the deployer to operate are down. Below are the options you can choose to resolve the issue.</p>
                </Message>
                <Segment>
                    <Grid columns={2} stackable textAlign='center'>
                        <Divider vertical>Or</Divider>
                        <Grid.Row verticalAlign='middle'>
                            <Grid.Column>
                                <p>I have resolved the deployer server configurations</p>
                                <Button primary onClick={onClickRetry}>Retry</Button>
                            </Grid.Column>
                            <Grid.Column>
                                <p>I want to connect to another deployer server instance</p>
                                <Button negative onClick={onClickDisconnect}>Disconnect</Button>
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Segment>
            </Segment>
            <Segment.Group horizontal>
                <Segment className="service-down-segment-left">
                    <Card padded={false}>
                        <div className="card-icon-container">
                            <Icon name='hdd outline' size='huge' />
                        </div>
                        <Card.Content>
                            <Card.Header>Storage</Card.Header>
                            <Card.Meta>
                                <span className='date'>Service required to backup and retrieve builds for deployment</span>
                            </Card.Meta>
                            <Card.Description>
                                <List>
                                    <List.Item>
                                        <List.Header></List.Header>
                                        {listRow('Status', StatusIcon(isStorageUP))}
                                        {listRow('Email', storageRef.email)}
                                        {listRow('ClientID', storageRef.ClientID)}
                                        {listRow('ClientSecret', storageRef.ClientSecret)}
                                        {listRow('Password', storageRef.password)}
                                        {listRow('Access Token', truncateIt(storageRef.AccessToken, 7))}
                                    </List.Item>
                                </List>
                            </Card.Description>
                        </Card.Content>
                        <Card.Content extra>
                            <a href title={storageError}>
                                <Icon name='info circle' />
                                Details
                            </a>
                        </Card.Content>
                    </Card>
                </Segment>
                <Segment className="service-down-segment-right">
                    <Card>
                        <div className="card-icon-container">
                            <Icon name='server' size='huge' />
                        </div>
                        <Card.Content>
                            <Card.Header>Database</Card.Header>
                            <Card.Meta>
                                <span className='date'>Service required for storing persistent data for this application</span>
                            </Card.Meta>
                            <Card.Description>
                                <List>
                                    <List.Item>
                                        <List.Header></List.Header>
                                        {listRow('Status', StatusIcon(isDatabaseUP))}
                                        {listRow('Host', dbRef.host)}
                                        {listRow('Username', dbRef.username)}
                                        {listRow('Password', dbRef.password)}
                                        {listRow('Primary DB', dbRef.database)}
                                        {listRow('', <div>&nbsp;</div>)}
                                    </List.Item>
                                </List>
                            </Card.Description>
                        </Card.Content>
                        <Card.Content extra>
                            <a href title={dbError}>
                                <Icon name='info circle' />
                                Details
                            </a>
                        </Card.Content>
                    </Card>
                </Segment>
            </Segment.Group>
        </Segment.Group>
    );
}

