import React, { Component } from 'react';
import _ from 'lodash';
import {
  Icon,
  Segment,
  Header,
  Message,
  Dropdown,
  Button
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'
import { deployerActionDirect } from '../Utils/APIUtil.js';
import { capitalize } from '../Utils/CommonUtil';
import { parseBuildInfo } from './CustomComponents';
import CONSTANTS from '../Utils/Constants';

const { ACTIONS } = CONSTANTS;

class DeployLKG extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedEntity: '',
      deployResponse: {
        text: 'We are fetching that content for you.',
        title: 'Just one second',
        isPositive: false
      },
      loading: true,
      backupData: {},
      tunnelsData: []
    }
  }

  onSelectChange = (value, stateName) => {
    this.setState({ [stateName]: value });
  }

  handleDeploy = async () => {
    this.setState({
      deployResponse: {
        text: 'Please wait while the build is being deployed.',
        title: 'Deploying',
        isPositive: true
      },
      loading: true
    });

    let response = await deployerActionDirect(ACTIONS.DEPLOY);
    response = _.get(response, 'data', {});
    const { logs, message, status } = response;

    const deployResponse = {
      text: logs.join('. '),
      title: message,
      isPositive: (status === 'success')
    };

    this.setState({
      loading: false,
      deployResponse
    });
  }

  async componentDidMount() {
    let backupData = await deployerActionDirect(ACTIONS.BACKUP_STATUS);
    let tunnelsData = await deployerActionDirect(ACTIONS.GET_TUNNELS);

    backupData = _.get(backupData, 'data', {});
    tunnelsData = _.get(tunnelsData, 'data.rows', []);

    this.setState({
      loading: false,
      backupData,
      tunnelsData,
      deployResponse: {
        text: 'We are ready to deploy the builds.',
        title: 'Ready',
        isPositive: true
      }
    });
  }

  _renderPage = () => {
    const { backupData, loading, deployResponse, tunnelsData, selectedEntity } = this.state;
    let disableButton = true;
    let _renderValidationMessage = <div />;
    const isEntitySelected = (selectedEntity !== '');

    const entityOptions = tunnelsData.map((item, index) => {
      return { key: (index + 1), text: `Deploy Latest ${capitalize(item.entity)}`, value: item.entity };
    });

    const entityDetails = _.find(tunnelsData, tunnel => {
      return (tunnel.entity === selectedEntity);
    }) || {};
    const remotePath = _.get(entityDetails, 'remote_path', '-');
    const entityName = _.get(entityDetails, 'entity', '-');

    let entiyBuildData = _.get(backupData, `data.${remotePath}`, []);
    entiyBuildData = _.filter(entiyBuildData, buildItem => {
      return !buildItem.name.includes('.FN.');
    });
    entiyBuildData = _.last(entiyBuildData) || {};
    const isBuildPresent = (_.get(entiyBuildData, 'path', null) !== null);

    disableButton = (!isBuildPresent);
    _renderValidationMessage = parseBuildInfo(`${capitalize(entityName)} LKG Build Information`,
      entiyBuildData, isBuildPresent, isEntitySelected, backupData.source);


    return (
      <Segment.Group>
        <Header as='h5' attached='top'>
          <div>Deploy LKG</div>
        </Header>
        <Segment>
          <Message>
            <Message.Header>What does it do?</Message.Header>
            <p>This workflow deploys the last known good builds.</p>
          </Message>
        </Segment>
        <Segment>
          <Dropdown
            disabled={loading}
            onChange={(e, { value }) => this.onSelectChange(value, 'selectedEntity')}
            options={entityOptions}
            placeholder='Choose an option'
            selection
            value={selectedEntity}
          />
        </Segment>
        <Segment>
          {_renderValidationMessage}
        </Segment>
        <Segment>
          <Button
            onClick={this.handleDeploy}
            disabled={disableButton || loading}
            loading={loading}
            primary>Deploy</Button>
          <Message icon positive={deployResponse.isPositive && !loading} negative={!deployResponse.isPositive && !loading}>
            <Icon name='circle notched' loading={loading} />
            <Message.Content>
              <Message.Header>{deployResponse.title}</Message.Header>
              {deployResponse.text}
            </Message.Content>
          </Message>
        </Segment>
      </Segment.Group>
    );
  }

  render() {
    return (this._renderPage());
  }
}

export default DeployLKG;

