import React from 'react';
import _ from 'lodash';
import {
    Button, Modal, Form, Input, Segment,
    Radio, Divider, Icon, Message, Popup
} from 'semantic-ui-react';
import { TransitionModal } from 'semantic-ui-react-transition-modal';
import {
    capitalize
} from '../Utils/CommonUtil';

const LABELS = {
    EMPTY: 'empty',
    DELETE: 'delete',
    INTACT: 'intact'
};

const DISPLAY_LABELS = {
    EMPTY: capitalize(LABELS.EMPTY),
    DELETE: capitalize(LABELS.DELETE),
    INTACT: capitalize(LABELS.INTACT)
}


export default function PurgeTunnel({ trigger, execute, selectedEntities, data }) {
    const [open, setOpen] = React.useState(false);
    const [valueRO, setRemoteOperation] = React.useState(LABELS.INTACT);
    const [valueLO, setLocalOperation] = React.useState(LABELS.INTACT);
    const [valueConfirmText, setConfirmText] = React.useState('');

    const handleChangeRO = (e, { value }) => setRemoteOperation(value);
    const handleChangeLO = (e, { value }) => setLocalOperation(value);
    const entityName = selectedEntities.join(", ");
    const entityNameCaps = entityName.toUpperCase();
    const enitityDetails = _.find(data, (tunnel) => {
        return (tunnel.entity === entityName);
    });

    const getIconColor = (value) => {
        switch (value) {
            case LABELS.DELETE:
                return 'red';
            case LABELS.EMPTY:
                return 'blink blue';
            default:
                return 'green';
        }
    }

    const deleteContextLocal = (details) => {
        const local_path = _.get(details, 'local_path');
        const contents_list = _.get(details, 'contents_list', []);
        if (_.isEmpty(contents_list)) {
            return <div>Local endpoint has not contents</div>;
        }

        switch (valueLO) {
            case LABELS.DELETE:
                const delete_contents = contents_list.map(cts => {
                    return <div className="to-be-deleted">{`/${local_path}/${cts}`}</div>
                });
                return delete_contents;
            case LABELS.EMPTY:
                const empty_contents = contents_list.map(cts => {
                    return <div className="not-to-be-deleted">{`/${local_path}/`}<span className="to-be-deleted">{`${cts}`}</span></div>
                });
                return empty_contents;
            default:
                const intact_contents = contents_list.map(cts => {
                    return <div className="not-to-be-deleted">{`/${local_path}/${cts}`}</div>
                });
                return intact_contents;
        }
    }
    const deleteContextRemote = (details) => {
        const remote_path = _.get(details, 'remote_path');
        const remote_contents_list = _.get(details, 'remote_contents_list', []);
        if (_.isEmpty(remote_contents_list)) {
            return <div>Remote endpoint has not contents</div>;
        }

        switch (valueRO) {
            case LABELS.DELETE:
                const delete_contents = remote_contents_list.map(cts => {
                    return <div className="to-be-deleted">{`/${remote_path}/${cts.name}`}</div>
                });
                return delete_contents;
            case LABELS.EMPTY:
                const empty_contents = remote_contents_list.map(cts => {
                    return (<div className="not-to-be-deleted">
                        {`/${remote_path}/`}
                        <span className="to-be-deleted">{`${cts.name}`}</span>
                    </div>);
                });
                return empty_contents;
            default:
                const intact_contents = remote_contents_list.map(cts => {
                    return <div className="not-to-be-deleted">{`/${remote_path}/${cts.name}`}</div>
                });
                return intact_contents;
        }
    }

    const getOperationDetails = (value) => {
        let msg = '';
        let typeProp = { info: true };
        switch (value) {
            case LABELS.DELETE:
                msg = 'This will delete the directory and it\'s contents';
                typeProp = { negative: true };
                break;
            case LABELS.EMPTY:
                msg = 'This will only delete the contents of the directory';
                typeProp = { info: true };
                break;
            default:
                msg = 'This will leave the endpoint intact';
                typeProp = { positive: true };
                break;
        }
        return (
            <Message {...typeProp}>
                {msg}
            </Message>
        );
    }

    return (
        <TransitionModal
            className="tunnel-purge-modal"
            closeOnDimmerClick={false}
            animation="fade up"
            duration={200}
            onClose={() => setOpen(false)}
            onOpen={() => setOpen(true)}
            open={open}
            trigger={trigger}
        >
            <Modal.Header>Purge Tunnel</Modal.Header>
            <Modal.Content>
                <Modal.Description>
                    <Segment.Group horizontal className="main-segment">
                        <Segment>
                            <label>Storage Endpoint</label>
                            <div className="icon-container">
                                <Popup
                                    content={deleteContextRemote(enitityDetails)}
                                    trigger={<Icon className={`${getIconColor(valueRO)}`}
                                        size="huge"
                                        disabled
                                        name='server' />}
                                />
                            </div>
                            <Form>
                                <label>Operation</label>
                                <Divider />
                                <Form.Group inline>
                                    <Form.Field
                                        control={Radio}
                                        label={DISPLAY_LABELS.DELETE}
                                        value={LABELS.DELETE}
                                        checked={valueRO === LABELS.DELETE}
                                        onChange={handleChangeRO}
                                    />
                                    <Form.Field
                                        control={Radio}
                                        label={DISPLAY_LABELS.EMPTY}
                                        value={LABELS.EMPTY}
                                        checked={valueRO === LABELS.EMPTY}
                                        onChange={handleChangeRO}
                                    />
                                    <Form.Field
                                        control={Radio}
                                        label={DISPLAY_LABELS.INTACT}
                                        value={LABELS.INTACT}
                                        checked={valueRO === LABELS.INTACT}
                                        onChange={handleChangeRO}
                                    />
                                </Form.Group>
                                {getOperationDetails(valueRO)}
                            </Form>
                        </Segment>
                        <Segment>
                            <label>Hosting Endpoint</label>
                            <div className="icon-container">
                                <Popup
                                    content={deleteContextLocal(enitityDetails)}
                                    trigger={
                                        <Icon
                                            disabled
                                            className={`${getIconColor(valueLO)}`}
                                            size="huge"
                                            name='folder outline'
                                        />
                                    }
                                />
                            </div>
                            <Form>
                                <label>Operation</label>
                                <Divider />
                                <Form.Group inline>
                                    <Form.Field
                                        control={Radio}
                                        label={DISPLAY_LABELS.DELETE}
                                        value={LABELS.DELETE}
                                        checked={valueLO === LABELS.DELETE}
                                        onChange={handleChangeLO}
                                    />
                                    <Form.Field
                                        control={Radio}
                                        label={DISPLAY_LABELS.EMPTY}
                                        value={LABELS.EMPTY}
                                        checked={valueLO === LABELS.EMPTY}
                                        onChange={handleChangeLO}
                                    />
                                    <Form.Field
                                        control={Radio}
                                        label={DISPLAY_LABELS.INTACT}
                                        value={LABELS.INTACT}
                                        checked={valueLO === LABELS.INTACT}
                                        onChange={handleChangeLO}
                                    />
                                </Form.Group>
                                {getOperationDetails(valueLO)}
                            </Form>
                        </Segment>
                    </Segment.Group>
                </Modal.Description>
            </Modal.Content>
            <Modal.Actions className="flex-row flex-jc-sb">
                <Input onChange={(e, { value }) => setConfirmText(value)} value={valueConfirmText} placeholder={entityName.toUpperCase()} />
                <div>
                    <Button onClick={() => setOpen(false)}>Cancel</Button>
                    <Button
                        disabled={entityNameCaps !== valueConfirmText}
                        onClick={() => {
                            setOpen(false);
                            execute({ local_action: valueLO, remote_action: valueRO });
                        }}
                        negative
                    >
                        Purge
                    </Button>
                </div>
            </Modal.Actions>
        </TransitionModal>
    )
}
