import React, { Component } from 'react';
import _ from 'lodash';
import {
  Popup,
  Label,
  Input,
  Dropdown,
  Icon,
  Button,
  Table,
  Segment,
  Header,
  Message,
  Pagination,
  Checkbox,
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'
import { deployerActionDirect } from '../Utils/APIUtil.js';
import {
  capitalize, successNotification, errorNotification,
  showBusyMessage, updateLoadingProgress, progressContinue
} from '../Utils/CommonUtil';
import { LoaderProgress, extraItemsPopup } from './CustomComponents';
import CONSTANTS from '../Utils/Constants';
import ModalAddTunnel from './ModalAddTunnel.jsx';
import DeleteTunnel from './DeleteTunnel.jsx';
import RevokeTunnel from './RevokeTunnel.jsx';
import PurgeTunnel from './PurgeTunnel.jsx';

const { ACTIONS } = CONSTANTS;
const ENTITY_TABLE = 'Tunnel';

export default class Tunnels extends Component {
  constructor(props) {
    super(props);

    this.tableRowLength = 5;

    this.state = {
      activePage: 1,
      value: 1,
      loading: true,
      tunnelsData: [],
      tunnelsDataSource: [],
      selectedEntities: [],
      loadingPercentage: 10
    }

    this.updateLoadingProgress = updateLoadingProgress.bind(this);
    this.progressContinue = progressContinue.bind(this);
  }

  async componentDidMount() {
    await this._getIntitalData();
  }

  componentWillUnmount() {
    clearInterval(this.timer);
  }

  setLoading = (value) => {
    this.setState({
      loading: value
    });
  }

  _getIntitalData = async () => {
    this.timer = setInterval(() => this.progressContinue(), 25);
    let tunnelsData = await deployerActionDirect(ACTIONS.GET_TUNNELS, { status: true });
    tunnelsData = _.get(tunnelsData, 'data.rows', []);
    this.updateLoadingProgress(90);
    clearInterval(this.timer);
    const tunnelsDataSource = tunnelsData;

    this.setState({
      loading: false,
      tunnelsData,
      tunnelsDataSource,
      selectedEntities: []
    });

    this.handlePaginationChange(null, { activePage: 1 });
  }

  handlePaginationChange = (e, { activePage }) => {
    let { tunnelsDataSource } = { ...this.state };
    let tunnelsData = [];
    for (let index = ((activePage - 1) * this.tableRowLength); index <= ((activePage * this.tableRowLength) - 1); index++) {
      if (index < tunnelsDataSource.length) {
        tunnelsData.unshift(tunnelsDataSource[index]);
      }
    }

    tunnelsData = _.sortBy(tunnelsData, (item) => item.entity);

    this.setState({
      tunnelsData,
      activePage
    });
  }

  onChangeSearch = (e) => {
    const search = e.target.value;
    let { tunnelsDataSource } = this.state;
    let tunnelsData = _.filter(tunnelsDataSource, tunnel => {
      return tunnel.entity.includes(search);
    });
    this.setState({
      tunnelsData,
      errors: []
    });
  }

  handleDeleteAction = async () => {
    const { selectedEntities } = this.state;
    const entity = _.first(selectedEntities);
    this.setLoading(true);
    let response = await deployerActionDirect(ACTIONS.DELETE_TUNNEL, { entity });
    response = _.get(response, 'data', {});
    const message = _.get(response, 'message', 'Deletion failed')
    if (response.status === "success") {
      successNotification(message);
    } else {
      errorNotification(message);
    }
    await this._getIntitalData();
  }

  handleRevokeAction = async () => {
    const { selectedEntities } = this.state;
    const entity = _.first(selectedEntities);
    this.setLoading(true);
    let response = await deployerActionDirect(ACTIONS.REVOKE_TUNNEL, { entity });
    response = _.get(response, 'data', {});
    const message = _.get(response, 'message', 'Revoke failed')
    if (response.status === "success") {
      successNotification(message);
    } else {
      errorNotification(message);
    }
    await this._getIntitalData();
  }

  handlePurgeAction = async (object = {}) => {
    debugger;
    const { selectedEntities } = this.state;
    const entity = _.first(selectedEntities);
    this.setLoading(true);
    const additionalParams = _.extend({ entity }, object);
    let response = await deployerActionDirect(ACTIONS.PURGE_TUNNEL, additionalParams);
    response = _.get(response, 'data', {});
    const message = _.get(response, 'message', 'Revoke failed')
    if (response.status === "success") {
      successNotification(message);
    } else {
      errorNotification(message);
    }
    await this._getIntitalData();
  }

  handleSelect = (username) => {
    let { selectedEntities } = { ...this.state };
    const entitySelected = _.includes(selectedEntities, username);
    if (entitySelected) {
      selectedEntities = _.remove(selectedEntities, function (entity) {
        return (entity !== username);
      });
    } else {
      selectedEntities.push(username);
    }
    this.setState({
      selectedEntities
    });
  }

  _formatContents = (contentsCSV) => {
    return extraItemsPopup(contentsCSV.split(','));
  }
  _formatPathStatus = (path, status, isEmpty) => {
    if (!status) {
      return (<div className="flex-row flex-jc-sb">
        <div>{`/${path}`}</div>
        <Popup
          content='This directory does not exist'
          trigger={<Icon className="yellow cursor-link" name='warning circle' />}
        />
      </div>);
    }
    if (isEmpty) {
      return (<div className="flex-row flex-jc-sb">
        <div>{`/${path}`}</div>
        <Popup
          content='This directory is empty'
          trigger={<Icon className="blue cursor-link" name='warning circle' />}
        />
      </div>);
    }
    return `/${path}`;
  }

  _renderTable = (data) => {
    let tableRowData = [
      {
        width: 1,
        header: '',
        dataPath: ['entity'],
        formatter: (entity) => (
          <Checkbox name={entity} onChange={() => this.handleSelect(entity)}
            checked={_.includes(this.state.selectedEntities, entity)} />)
      },
      {
        width: 4,
        header: 'Entity',
        dataPath: ['entity', 'type'],
        formatter: (entity, type) => {
          if (type === 'default') {
            return (
              <div>{capitalize(entity)}
                <Label className="ml-5" as='a' basic color='blue'>{capitalize(type)}</Label>
              </div>
            );
          }
          return capitalize(entity);
        }
      },
      {
        width: 2,
        header: 'Hosting Endpoint',
        dataPath: ['local_path', 'local_status', 'is_local_empty'],
        formatter: this._formatPathStatus
      },
      {
        width: 4,
        header: 'Storage Endpoint',
        dataPath: ['remote_path', 'remote_status', 'is_remote_empty'],
        formatter: this._formatPathStatus
      },
      {
        width: 2,
        header: 'Contents',
        dataPath: ['contents'],
        formatter: this._formatContents
      }
    ];

    const tableData = data.map(row => {
      return tableRowData.map(item => {
        const hasFormater = _.get(item, 'formatter', null);
        if (hasFormater) {
          // Handle if function output is passed instead of reference
          try {
            const results = item.dataPath.map(dp => {
              if (dp === '[object]') {
                return row;
              }
              return _.get(row, dp, '-');
            });
            return hasFormater(...results);
          } catch {
            return hasFormater || '-';
          }
        }
        return _.get(row, item.dataPath[0], '-');
      });
    });

    if (_.isEmpty(data)) {
      return (LoaderProgress(this.state.loadingPercentage));
    }

    return (
      <div>
        <div className="w-100 flex-row flex-jc-fe pt-10">
          <Pagination
            firstItem={null}
            lastItem={null}
            activePage={this.state.activePage}
            onPageChange={this.handlePaginationChange}
            totalPages={Math.ceil(this.state.tunnelsDataSource.length / this.tableRowLength)}
          />
        </div>
        <Table celled>
          <Table.Header>
            <Table.Row>
              {
                tableRowData.map(item => {
                  return <Table.HeaderCell width={item.width}>{item.header}</Table.HeaderCell>;
                })
              }
            </Table.Row>
          </Table.Header>

          <Table.Body>
            {
              tableData.map(row => {
                return <Table.Row>{
                  row.map(cell => {
                    return <Table.Cell>{cell}</Table.Cell>
                  })
                }</Table.Row>;
              })
            }
          </Table.Body>
        </Table>
      </div>
    )
  }

  _renderTableMessage = (data) => {
    const { loading } = this.state;
    let content = <p>Viewing all {data.length} {ENTITY_TABLE}</p>;
    if (loading) {
      content = <p>{<Icon name='circle notched blue' loading />}{showBusyMessage()}</p>;
    }
    return (<Message>{content}</Message>);
  }

  _renderPage = () => {
    const { tunnelsData, tunnelsDataSource,
      selectedEntities } = this.state;

    return (
      <Segment.Group>
        <Header as='h5' attached='top'>
          <div>{ENTITY_TABLE}</div>
        </Header>
        <Segment>
          <div className="flex-row flex-jc-sb w-100">
            <div>
              <ModalAddTunnel
                refreshTable={this._getIntitalData}
                trigger={<Button icon labelPosition='right' primary>
                  <Icon name='add' />
                  {`Create ${ENTITY_TABLE}`}
                </Button>} />
              <Dropdown
                disabled={(selectedEntities.length !== 1)}
                button
                text='Actions'>
                <Dropdown.Menu closeOnBlur={true} closeOnChange={true}>
                  <DeleteTunnel
                    execute={this.handleDeleteAction}
                    selectedEntities={selectedEntities}
                    trigger={< Dropdown.Item disabled={(selectedEntities.length !== 1)} text='Delete' />} />
                  <RevokeTunnel
                    execute={this.handleRevokeAction}
                    selectedEntities={selectedEntities}
                    trigger={< Dropdown.Item disabled={(selectedEntities.length !== 1)} text='Revoke' />} />
                  <PurgeTunnel
                    data={tunnelsDataSource}
                    execute={this.handlePurgeAction}
                    selectedEntities={selectedEntities}
                    trigger={< Dropdown.Item disabled={(selectedEntities.length !== 1)} text='Purge' />} />
                  <Dropdown.Item disabled={(selectedEntities.length !== 1)} text='Update' />
                </Dropdown.Menu>
              </Dropdown>
            </div>
            <Input icon='search' onChange={(e) => this.onChangeSearch(e)} placeholder='Search...' />
          </div>
          {this._renderTableMessage(tunnelsDataSource)}
        </Segment>
        <Segment className="table-segment">
          {this._renderTable(tunnelsData)}
        </Segment>
      </Segment.Group>
    );
  }

  render() {
    return (this._renderPage());
  }
}
