import React, { Component } from 'react';
import { Button, Message, Form, Modal, Input, Dropdown } from 'semantic-ui-react';
import { TransitionModal } from 'semantic-ui-react-transition-modal';
import _ from 'lodash';
import { deployerActionDirect } from '../Utils/APIUtil.js';
import { successNotification, i18n, i18nResolver, trimChar } from '../Utils/CommonUtil';
import { ModalHeader } from './CustomComponents';

import CONSTANTS from '../Utils/Constants';

const { ACTIONS } = CONSTANTS;

export default class ModalAddTunnel extends Component {
  state = {
    open: false,
    loadingContentOptions: true,
    tunnelSchema: [],
    errors: []
  }

  onChange = (e, stateName) => {
    const value = e.target.value;
    let stateObject = { ...this.state };
    let updateObject = {};
    switch (stateName) {
      case 'entity':
        updateObject = {
          [stateName]: value,
          'remote_path': `${value.toUpperCase()}_BACKUP`
        };
        stateObject = _.extend(stateObject, updateObject);
        this.setState(stateObject);
        break;
      default:
        this.setState({
          [stateName]: value,
          errors: []
        });
        break;
    }
  }

  onLocalPathChange = (e) => {
    const search = e.target.value;
    this.setState({
      'local_path': search,
      contentOptions: [],
      loadingContentOptions: true
    });

    const setContentsDef = (searchx) => {
      this.setContents(searchx);
    }

    const searchDebounce = _.debounce(function (searchx) {
      setContentsDef(searchx);
    }, 700);
    searchDebounce(search);
  }

  onSelectChange = (value, stateName) => {
    this.setState({
      [stateName]: value,
      errors: []
    });
  }

  fetchPaths = async (path) => {
    let contents = await deployerActionDirect(ACTIONS.GET_DIR_CONTENTS, { path });
    contents = _.get(contents, 'data.contents', []);
    return contents;
  }

  setContents = async (path) => {
    let contents = await deployerActionDirect(ACTIONS.GET_DIR_CONTENTS, { path });
    contents = _.get(contents, 'data.contents', []);
    this.setState({
      contentOptions: contents,
      loadingContentOptions: false
    });
  }

  setOpen = async () => {
    let tunnelSchema = await deployerActionDirect(ACTIONS.SCHEMA, { table: 'tunnels' });
    let contentOptions = await this.fetchPaths('');
    tunnelSchema = _.get(tunnelSchema, 'data.columns', []);

    const ignoreFields = [
      'type'
    ];
    tunnelSchema = _.remove(tunnelSchema, schema => {
      return !ignoreFields.includes(schema);
    });

    let schemaStates = {};
    // Populate Default fields if any here
    tunnelSchema.forEach(field => {
      let defaultValue = null;
      if (field === 'local_path') {
        defaultValue = '';
      }
      if (field === 'contents') {
        defaultValue = [];
      }
      schemaStates = _.extend(schemaStates, { [field]: defaultValue });

    });

    const stateObject = _.extend(schemaStates, {
      loading: false,
      loadingContentOptions: false,
      contentOptions,
      tunnelSchema,
      open: true
    });

    this.setState(stateObject);
  }

  setClose = () => {
    this.setState({ open: false });
  }

  handleSubmit = async () => {
    const { tunnelSchema } = this.state;
    let payload = {};
    tunnelSchema.forEach(item => {
      let key = item;
      let value = _.get(this.state, item, '');
      if (item === 'contents') {
        value = _.get(this.state, item, []);
        value = value.join(',');
      }
      payload = _.extend(payload, { [key]: value });
    });
    let response = await deployerActionDirect(ACTIONS.ADD_TUNNEL, payload);
    response = _.get(response, 'data', {});

    if (response.status === "success") {
      this.setClose();
      this.props.refreshTable();
      successNotification(response.message);
    } else {
      const errors = [];
      errors.push(i18nResolver(_.get(response, 'error', '')));
      this.setState({
        errors
      });
    }
  }

  _inputContentsDropdown = () => {
    const options = this.state.contentOptions.map(item => {
      return { key: item.name, text: item.name, value: item.name };
    })
    return (
      <Dropdown
        placeholder='All Content'
        value={this.state.contents}
        fluid
        multiple
        loading={this.state.loadingContentOptions}
        selection
        onChange={(e, { value }) => this.onSelectChange(value, 'contents')}
        options={options}
      />);
  }

  _populateFormFields = () => {
    const { tunnelSchema, contentOptions, local_path, loadingContentOptions } = this.state;

    const fields = tunnelSchema.map(column => {
      switch (column) {
        case 'contents':
          return (
            <Form.Field>
              <label>{i18n(column)}</label>
              {this._inputContentsDropdown()}
            </Form.Field>
          );
        case 'local_path':
          return (
            <Form.Field>
              <label>{i18n(column)}</label>
              <Input
                loading={loadingContentOptions}
                list="paths"
                label="/"
                placeholder={i18n(column)}
                onChange={(e) => this.onLocalPathChange(e)}
                value={this.state[column]} />
              <datalist id="paths">
                {
                  contentOptions.map(item => {
                    if (item.tag === 'folder') {
                      return <option value={trimChar(`${local_path}/${item.name}`, '/')} />;
                    }
                    return null;
                  })
                }
              </datalist>
            </Form.Field>
          );
        default:
          return (
            <Form.Field>
              <label>{i18n(column)}</label>
              <input placeholder={i18n(column)} onChange={(e) => this.onChange(e, column)} value={this.state[column]} />
            </Form.Field>
          );
      }
    });

    return fields;
  }

  render() {
    return (
      <TransitionModal
        className="fullpage-modal"
        closeOnDimmerClick={false}
        animation="fade up"
        duration={200}
        onClose={() => this.setClose()}
        onOpen={() => this.setOpen()}
        open={this.state.open}
        trigger={this.props.trigger}
      >
        <Modal.Header>{ModalHeader('Create Tunnel', this.setClose)}</Modal.Header>
        <Modal.Content>
          <Modal.Description>
            <div className="add-form-body w-50">
              <Form error>
                <Message
                  className={this.state.errors.length > 0 ? 'alert visible' : 'alert invisible'}
                  error
                  header='Failed'
                  content={this.state.errors.join('. ')}
                />
                {this._populateFormFields()}
              </Form>
            </div>
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button onClick={() => this.setClose()}>
            Cancel
          </Button>
          <Button
            content="Submit"
            onClick={() => this.handleSubmit()}
            positive
          />
        </Modal.Actions>
      </TransitionModal>
    )
  }
}
